<template>
  <Teleport to="body">
    <Transition name="modal">
      <div v-if="isOpen && project" class="modal-scrollable fixed inset-0 z-50 overflow-y-auto">
        <!-- Backdrop -->
        <div
          class="fixed inset-0 bg-black/60 backdrop-blur-sm transition-opacity duration-300"
          @click="closeModal"
        ></div>

        <!-- Modal Content -->
        <div class="flex min-h-full items-center justify-center p-4" @click="closeModal">
          <div
            class="relative max-h-[90vh] w-full max-w-4xl transform overflow-hidden rounded-2xl bg-white shadow-2xl transition-all duration-300 dark:bg-gray-900"
            @click.stop
          >
            <!-- Close Button -->
            <button
              @click="closeModal"
              class="absolute right-4 top-4 z-10 flex h-10 w-10 items-center justify-center rounded-full bg-gray-100 transition-colors duration-200 hover:bg-gray-200 dark:bg-gray-800 dark:hover:bg-gray-700"
            >
              <FontAwesomeIcon icon="times" class="h-5 w-5 text-gray-600 dark:text-gray-400" />
            </button>

            <!-- Modal Body -->
            <div class="modal-scrollable max-h-[90vh] overflow-y-auto">
              <!-- Hero Section -->
              <div class="relative h-64 overflow-hidden" ref="heroImageWrapper">
                <div
                  v-if="!isHeroImageVisible"
                  class="absolute inset-0 animate-pulse bg-gray-200 dark:bg-gray-800"
                ></div>
                <img
                  v-if="isHeroImageVisible && baseImageUrl"
                  :src="baseImageUrl"
                  :alt="project.title"
                  class="absolute inset-0 h-full w-full object-cover"
                  loading="lazy"
                  decoding="async"
                />
                <div
                  class="absolute inset-0 bg-gradient-to-t from-black/60 via-black/20 to-transparent"
                ></div>
                <div class="absolute bottom-6 left-6 text-white">
                  <h2 class="mb-2 text-3xl font-bold">{{ project.title }}</h2>
                  <div class="flex items-center space-x-4 text-sm">
                    <span class="rounded-full bg-white/20 px-3 py-1 backdrop-blur-sm">
                      {{ project.category }}
                    </span>
                    <span class="rounded-full bg-white/20 px-3 py-1 backdrop-blur-sm">
                      {{ project.year }}
                    </span>
                    <span class="rounded-full px-3 py-1" :class="getStatusClass(project.status)">
                      {{ project.status }}
                    </span>
                  </div>
                </div>
              </div>

              <!-- Content -->
              <div class="space-y-8 p-6">
                <!-- Description -->
                <section>
                  <h3 class="mb-4 text-xl font-semibold text-gray-900 dark:text-white">
                    {{ tUI('projects.modal.description') }}
                  </h3>
                  <div class="space-y-3 leading-relaxed text-gray-600 dark:text-gray-300">
                    <template v-if="formattedDescriptionSections.length">
                      <template
                        v-for="(section, index) in formattedDescriptionSections"
                        :key="index"
                      >
                        <p v-if="section.type === 'paragraph'">
                          {{ section.content[0] }}
                        </p>
                        <ul
                          v-else
                          class="list-disc space-y-1 pl-5 text-gray-600 marker:text-primary-500 dark:text-gray-300"
                        >
                          <li
                            v-for="(item, itemIndex) in section.content"
                            :key="`${index}-${itemIndex}`"
                          >
                            {{ item }}
                          </li>
                        </ul>
                      </template>
                    </template>
                    <p v-else>
                      {{ project.longDescription }}
                    </p>
                  </div>
                </section>

                <!-- Project Details -->
                <div class="grid grid-cols-1 gap-6 md:grid-cols-2">
                  <!-- Left Column -->
                  <div class="space-y-6">
                    <!-- Technologies -->
                    <section>
                      <h3 class="mb-3 text-lg font-semibold text-gray-900 dark:text-white">
                        {{ tUI('projects.modal.technologies') }}
                      </h3>
                      <div class="flex flex-wrap gap-2">
                        <span
                          v-for="tech in project.technologies"
                          :key="tech"
                          class="rounded-full bg-primary-100 px-3 py-1 text-sm font-medium text-primary-700 dark:bg-primary-900 dark:text-primary-300"
                        >
                          {{ tech }}
                        </span>
                      </div>
                    </section>

                    <!-- Project Info -->
                    <section>
                      <h3 class="mb-3 text-lg font-semibold text-gray-900 dark:text-white">
                        {{ tUI('projects.modal.projectInfo') }}
                      </h3>
                      <div class="space-y-3">
                        <div class="flex justify-between">
                          <span class="text-gray-600 dark:text-gray-400"
                            >{{ tUI('projects.modal.duration') }}:</span
                          >
                          <span class="font-medium text-gray-900 dark:text-white">{{
                            project.duration
                          }}</span>
                        </div>
                        <div class="flex justify-between">
                          <span class="text-gray-600 dark:text-gray-400"
                            >{{ tUI('projects.modal.team') }}:</span
                          >
                          <span class="font-medium text-gray-900 dark:text-white">{{
                            project.team
                          }}</span>
                        </div>
                        <div class="flex justify-between">
                          <span class="text-gray-600 dark:text-gray-400"
                            >{{ tUI('projects.modal.role') }}:</span
                          >
                          <span class="font-medium text-gray-900 dark:text-white">{{
                            project.role
                          }}</span>
                        </div>
                      </div>
                    </section>
                  </div>

                  <!-- Right Column -->
                  <div class="space-y-6">
                    <!-- Performance Metrics -->
                    <section v-if="project.metrics">
                      <h3 class="mb-3 text-lg font-semibold text-gray-900 dark:text-white">
                        {{ tUI('projects.modal.metrics') }}
                      </h3>
                      <div class="space-y-3">
                        <div class="flex items-center justify-between">
                          <span class="text-gray-600 dark:text-gray-400">{{
                            tUI('projects.metrics.performance')
                          }}</span>
                          <div class="flex items-center space-x-2">
                            <div
                              class="h-2 w-20 overflow-hidden rounded-full bg-gray-200 dark:bg-gray-700"
                            >
                              <div
                                class="h-full rounded-full bg-green-500 transition-all duration-1000"
                                :style="{ width: project.metrics.performance + '%' }"
                              ></div>
                            </div>
                            <span class="text-sm font-medium text-gray-900 dark:text-white">
                              {{ project.metrics.performance }}%
                            </span>
                          </div>
                        </div>
                        <div class="flex items-center justify-between">
                          <span class="text-gray-600 dark:text-gray-400">{{
                            tUI('projects.metrics.accessibility')
                          }}</span>
                          <div class="flex items-center space-x-2">
                            <div
                              class="h-2 w-20 overflow-hidden rounded-full bg-gray-200 dark:bg-gray-700"
                            >
                              <div
                                class="h-full rounded-full bg-blue-500 transition-all duration-1000"
                                :style="{ width: project.metrics.accessibility + '%' }"
                              ></div>
                            </div>
                            <span class="text-sm font-medium text-gray-900 dark:text-white">
                              {{ project.metrics.accessibility }}%
                            </span>
                          </div>
                        </div>
                        <div class="flex items-center justify-between">
                          <span class="text-gray-600 dark:text-gray-400">{{
                            tUI('projects.metrics.seo')
                          }}</span>
                          <div class="flex items-center space-x-2">
                            <div
                              class="h-2 w-20 overflow-hidden rounded-full bg-gray-200 dark:bg-gray-700"
                            >
                              <div
                                class="h-full rounded-full bg-yellow-500 transition-all duration-1000"
                                :style="{ width: project.metrics.seo + '%' }"
                              ></div>
                            </div>
                            <span class="text-sm font-medium text-gray-900 dark:text-white">
                              {{ project.metrics.seo }}%
                            </span>
                          </div>
                        </div>
                        <div class="flex items-center justify-between">
                          <span class="text-gray-600 dark:text-gray-400">{{
                            tUI('projects.metrics.bestPractices')
                          }}</span>
                          <div class="flex items-center space-x-2">
                            <div
                              class="h-2 w-20 overflow-hidden rounded-full bg-gray-200 dark:bg-gray-700"
                            >
                              <div
                                class="h-full rounded-full bg-purple-500 transition-all duration-1000"
                                :style="{ width: project.metrics.best_practices + '%' }"
                              ></div>
                            </div>
                            <span class="text-sm font-medium text-gray-900 dark:text-white">
                              {{ project.metrics.best_practices }}%
                            </span>
                          </div>
                        </div>
                      </div>
                    </section>

                    <!-- Links -->
                    <section>
                      <h3 class="mb-3 text-lg font-semibold text-gray-900 dark:text-white">
                        {{ tUI('projects.modal.links') }}
                      </h3>
                      <div class="space-y-3">
                        <template v-if="hasVisibleLinks">
                          <a
                            v-if="project.links.demo"
                            :href="project.links.demo"
                            target="_blank"
                            class="flex items-center space-x-3 rounded-lg bg-gray-50 p-3 transition-colors duration-200 hover:bg-gray-100 dark:bg-gray-800 dark:hover:bg-gray-700"
                          >
                            <FontAwesomeIcon
                              icon="external-link-alt"
                              class="h-4 w-4 text-primary-600"
                            />
                            <span class="font-medium text-gray-900 dark:text-white">{{
                              tUI('projects.modal.viewDemo')
                            }}</span>
                          </a>
                          <a
                            v-if="project.links.figma"
                            :href="project.links.figma"
                            target="_blank"
                            class="flex items-center space-x-3 rounded-lg bg-gray-50 p-3 transition-colors duration-200 hover:bg-gray-100 dark:bg-gray-800 dark:hover:bg-gray-700"
                          >
                            <FontAwesomeIcon
                              icon="fab fa-figma"
                              class="h-4 w-4 text-pink-500 dark:text-pink-400"
                            />
                            <span class="font-medium text-gray-900 dark:text-white">{{
                              tUI('projects.modal.viewDesign')
                            }}</span>
                          </a>
                          <a
                            v-if="project.links.github"
                            :href="project.links.github"
                            target="_blank"
                            class="flex items-center space-x-3 rounded-lg bg-gray-50 p-3 transition-colors duration-200 hover:bg-gray-100 dark:bg-gray-800 dark:hover:bg-gray-700"
                          >
                            <FontAwesomeIcon
                              icon="fab fa-github"
                              class="h-4 w-4 text-gray-900 dark:text-white"
                            />
                            <span class="font-medium text-gray-900 dark:text-white">{{
                              tUI('projects.modal.viewCode')
                            }}</span>
                          </a>
                        </template>
                        <p v-else class="text-sm text-gray-500 dark:text-gray-400">
                          {{ tUI('projects.modal.noLinksAvailable') }}
                        </p>
                      </div>
                    </section>
                  </div>
                </div>

                <!-- Challenges & Solutions -->
                <div class="grid grid-cols-1 gap-6 lg:grid-cols-2">
                  <!-- Challenges -->
                  <section v-if="project.challenges && project.challenges.length > 0">
                    <h3 class="mb-3 text-lg font-semibold text-gray-900 dark:text-white">
                      {{ tUI('projects.modal.challenges') }}
                    </h3>
                    <ul class="space-y-2">
                      <li
                        v-for="(challenge, index) in project.challenges"
                        :key="index"
                        class="flex items-start space-x-3"
                      >
                        <FontAwesomeIcon
                          icon="exclamation-triangle"
                          class="mt-1 h-4 w-4 flex-shrink-0 text-orange-500"
                        />
                        <span class="text-gray-600 dark:text-gray-300">{{ challenge }}</span>
                      </li>
                    </ul>
                  </section>

                  <!-- Solutions -->
                  <section v-if="project.solutions && project.solutions.length > 0">
                    <h3 class="mb-3 text-lg font-semibold text-gray-900 dark:text-white">
                      {{ tUI('projects.modal.solutions') }}
                    </h3>
                    <ul class="space-y-2">
                      <li
                        v-for="(solution, index) in project.solutions"
                        :key="index"
                        class="flex items-start space-x-3"
                      >
                        <FontAwesomeIcon
                          icon="check-circle"
                          class="mt-1 h-4 w-4 flex-shrink-0 text-green-500"
                        />
                        <span class="text-gray-600 dark:text-gray-300">{{ solution }}</span>
                      </li>
                    </ul>
                  </section>
                </div>

                <!-- Results -->
                <section v-if="project.results && project.results.length > 0">
                  <h3 class="mb-3 text-lg font-semibold text-gray-900 dark:text-white">
                    {{ tUI('projects.modal.results') }}
                  </h3>
                  <div class="grid grid-cols-1 gap-4 md:grid-cols-3">
                    <div
                      v-for="(result, index) in project.results"
                      :key="index"
                      class="rounded-lg border border-primary-200 bg-gradient-to-br from-primary-50 to-primary-100 p-4 dark:border-primary-700 dark:from-primary-900/30 dark:to-primary-800/30"
                    >
                      <FontAwesomeIcon
                        icon="trophy"
                        class="mb-2 h-5 w-5 text-primary-600 dark:text-primary-400"
                      />
                      <p class="text-sm text-gray-700 dark:text-gray-300">{{ result }}</p>
                    </div>
                  </div>
                </section>

                <!-- Project Gallery -->
                <section v-if="project.images && project.images.length > 0">
                  <h3 class="mb-3 text-lg font-semibold text-gray-900 dark:text-white">
                    {{ tUI('projects.modal.gallery') }}
                  </h3>
                  <div class="grid grid-cols-2 gap-4 md:grid-cols-3">
                    <div
                      v-for="(image, index) in project.images"
                      :key="index"
                      :ref="(el) => setGalleryRef(el, index)"
                      class="relative h-32 w-full cursor-pointer overflow-hidden rounded-lg"
                      @click="openImageModal(image)"
                    >
                      <div
                        v-if="!isGalleryImageVisible(index)"
                        class="absolute inset-0 animate-pulse bg-gray-200 dark:bg-gray-700"
                      ></div>
                      <img
                        v-if="isGalleryImageVisible(index)"
                        :src="getGalleryImageUrl(image)"
                        :alt="`${project.title} - Image ${index + 1}`"
                        class="absolute inset-0 h-full w-full object-cover transition-opacity duration-200 hover:opacity-80"
                        loading="lazy"
                        decoding="async"
                      />
                    </div>
                  </div>
                </section>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Transition>

    <Transition name="fade">
      <div
        v-if="isImageModalOpen && selectedImage"
        class="fixed inset-0 z-[60] flex items-center justify-center bg-black/80 backdrop-blur"
        @click="closeImageModal"
      >
        <div class="relative max-h-[90vh] w-full max-w-4xl px-4" @click.stop>
          <!-- Close Button -->
          <button
            type="button"
            @click="closeImageModal"
            class="absolute right-6 top-6 flex h-10 w-10 items-center justify-center rounded-full bg-black/60 text-white transition-colors duration-200 hover:bg-black/80"
            aria-label="Close image preview"
          >
            <FontAwesomeIcon icon="times" class="h-4 w-4" />
          </button>

          <!-- Previous Button -->
          <button
            v-if="canGoToPrevious"
            type="button"
            @click="goToPreviousImage"
            class="absolute left-0 top-1/2 flex h-12 w-12 -translate-x-12 -translate-y-1/2 items-center justify-center rounded-full bg-black/60 text-white transition-all duration-200 hover:scale-110 hover:bg-black/80"
            aria-label="Previous image"
          >
            <FontAwesomeIcon icon="chevron-left" class="h-6 w-6" />
          </button>

          <!-- Next Button -->
          <button
            v-if="canGoToNext"
            type="button"
            @click="goToNextImage"
            class="absolute right-0 top-1/2 flex h-12 w-12 -translate-y-1/2 translate-x-12 items-center justify-center rounded-full bg-black/60 text-white transition-all duration-200 hover:scale-110 hover:bg-black/80"
            aria-label="Next image"
          >
            <FontAwesomeIcon icon="chevron-right" class="h-6 w-6" />
          </button>

          <!-- Image Counter -->
          <div
            v-if="currentImageIndex >= 0"
            class="absolute bottom-4 left-1/2 -translate-x-1/2 rounded-full bg-black/60 px-4 py-2 text-sm text-white"
          >
            {{ currentImageIndex + 1 }} / {{ project?.images?.length ?? 0 }}
          </div>

          <!-- Main Image with Slide Animation -->
          <Transition :name="`slide-${slideDirection}`" mode="out-in">
            <img
              :key="currentImageIndex"
              :src="getGalleryImageUrl(selectedImage)"
              :alt="`${project?.title ?? 'Project'} preview`"
              class="max-h-[80vh] w-full rounded-xl object-contain shadow-2xl"
              loading="lazy"
              decoding="async"
            />
          </Transition>
        </div>
      </div>
    </Transition>
  </Teleport>
</template>

<script setup lang="ts">
import { computed, nextTick, onUnmounted, ref, watch } from 'vue'
import type { ComponentPublicInstance } from 'vue'
import { useI18n } from '@/composables/useI18n'

// Compute the correct main image URL for dev/prod
const baseImageUrl = computed(() => {
  if (!props.project?.image) return ''
  const img = props.project.image.startsWith('/')
    ? props.project.image.slice(1)
    : props.project.image
  return import.meta.env.BASE_URL + img
})

// Compute the correct gallery image URL for dev/prod
function getGalleryImageUrl(image: string | null): string {
  if (!image) return ''
  const img = image.startsWith('/') ? image.slice(1) : image
  return import.meta.env.BASE_URL + img
}

interface Project {
  id: string
  title: string
  description: string
  longDescription: string
  image: string
  images?: string[]
  technologies: string[]
  category: string
  status: string
  year: string
  duration: string
  team: string
  role: string
  challenges?: string[]
  solutions?: string[]
  results?: string[]
  links: {
    demo?: string
    github?: string
    case_study?: string
    figma?: string
  }
  metrics?: {
    performance: number
    accessibility: number
    seo: number
    best_practices: number
  }
}

interface Props {
  isOpen: boolean
  project: Project | null
}

interface Emits {
  (e: 'close'): void
}

type FormattedDescriptionSection = {
  type: 'paragraph' | 'list'
  content: string[]
}

const linkKeys: Array<keyof Project['links']> = ['demo', 'github', 'figma']

const props = defineProps<Props>()
const emit = defineEmits<Emits>()

const { tUI } = useI18n()

const isImageModalOpen = ref(false)
const selectedImage = ref<string | null>(null)
const currentImageIndex = ref(-1)
const slideDirection = ref<'left' | 'right'>('right')

const heroImageWrapper = ref<HTMLElement | null>(null)
const isHeroImageVisible = ref(false)
const galleryItemRefs = ref<(HTMLElement | null)[]>([])
const galleryVisibility = ref<Record<number, boolean>>({})
let heroImageObserver: IntersectionObserver | null = null
let galleryObserver: IntersectionObserver | null = null

const setGalleryRef = (el: Element | ComponentPublicInstance | null, index: number) => {
  galleryItemRefs.value[index] = el as HTMLElement | null
}

const isGalleryImageVisible = (index: number) => {
  return galleryVisibility.value[index] ?? false
}

const cleanupObservers = () => {
  if (heroImageObserver) {
    heroImageObserver.disconnect()
    heroImageObserver = null
  }

  if (galleryObserver) {
    galleryObserver.disconnect()
    galleryObserver = null
  }
}

const setupHeroImageObserver = () => {
  if (!props.isOpen) {
    isHeroImageVisible.value = false
    return
  }

  if (typeof window === 'undefined') {
    isHeroImageVisible.value = true
    return
  }

  if (!heroImageWrapper.value) {
    isHeroImageVisible.value = true
    return
  }

  if (!('IntersectionObserver' in window)) {
    isHeroImageVisible.value = true
    return
  }

  heroImageObserver?.disconnect()
  isHeroImageVisible.value = false

  heroImageObserver = new IntersectionObserver(
    (entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          isHeroImageVisible.value = true
          heroImageObserver?.disconnect()
          heroImageObserver = null
        }
      })
    },
    {
      rootMargin: '40px',
      threshold: 0.2,
    }
  )

  heroImageObserver.observe(heroImageWrapper.value)
}

const setupGalleryObserver = () => {
  galleryVisibility.value = {}

  if (!props.isOpen || !props.project?.images?.length) {
    return
  }

  if (typeof window === 'undefined' || !('IntersectionObserver' in window)) {
    galleryItemRefs.value.forEach((_el, index) => {
      galleryVisibility.value[index] = true
    })
    return
  }

  galleryObserver?.disconnect()
  galleryObserver = new IntersectionObserver(
    (entries) => {
      entries.forEach((entry) => {
        const indexAttr = (entry.target as HTMLElement).dataset.galleryIndex
        if (indexAttr && entry.isIntersecting) {
          const index = Number(indexAttr)
          galleryVisibility.value[index] = true
          galleryObserver?.unobserve(entry.target as HTMLElement)
        }
      })
    },
    {
      rootMargin: '40px',
      threshold: 0.2,
    }
  )

  galleryItemRefs.value.forEach((el, index) => {
    if (!el) return
    el.dataset.galleryIndex = index.toString()
    galleryVisibility.value[index] = false
    galleryObserver?.observe(el)
  })
}

const formattedDescriptionSections = computed<FormattedDescriptionSection[]>(() => {
  const description = props.project?.longDescription?.trim()
  if (!description) {
    return []
  }

  const sections: FormattedDescriptionSection[] = []
  let currentList: string[] = []
  let isCollectingList = false

  description.split('\n').forEach((rawLine) => {
    const line = rawLine.trim()

    if (!line) {
      if (isCollectingList && currentList.length > 0) {
        sections.push({ type: 'list', content: currentList })
        currentList = []
        isCollectingList = false
      }
      return
    }

    if (line.startsWith('- ')) {
      if (!isCollectingList) {
        currentList = []
        isCollectingList = true
      }
      currentList.push(line.substring(2).trim())
      return
    }

    if (isCollectingList && currentList.length > 0) {
      sections.push({ type: 'list', content: currentList })
      currentList = []
      isCollectingList = false
    }

    sections.push({ type: 'paragraph', content: [line] })
  })

  if (isCollectingList && currentList.length > 0) {
    sections.push({ type: 'list', content: currentList })
  }

  return sections
})

const hasVisibleLinks = computed(() => {
  const links = props.project?.links
  if (!links) {
    return false
  }

  return linkKeys.some((key) => {
    const value = links[key]
    if (typeof value === 'string') {
      return value.trim().length > 0
    }
    return Boolean(value)
  })
})

// Computed properties pour la navigation
const canGoToPrevious = computed(() => currentImageIndex.value > 0)
const canGoToNext = computed(
  () => currentImageIndex.value < (props.project?.images?.length ?? 0) - 1
)

// Nettoyer au démontage du composant
onUnmounted(() => {
  document.body.style.overflow = ''
  document.removeEventListener('keydown', handleKeyDown)
  cleanupObservers()
})

// Gestion de la touche Escape pour fermer le modal
const handleKeyDown = (event: KeyboardEvent) => {
  if (event.key === 'Escape') {
    if (isImageModalOpen.value) {
      closeImageModal()
      return
    }

    if (props.isOpen) {
      closeModal()
    }
  }

  // Gestion des touches fléchées pour la navigation dans la galerie
  if (isImageModalOpen.value) {
    if (event.key === 'ArrowLeft' && canGoToPrevious.value) {
      goToPreviousImage()
    } else if (event.key === 'ArrowRight' && canGoToNext.value) {
      goToNextImage()
    }
  }
}

// Ajouter/supprimer l'event listener selon l'état du modal
watch(
  () => props.isOpen,
  async (isOpen) => {
    if (isOpen) {
      document.body.style.overflow = 'hidden'
      document.addEventListener('keydown', handleKeyDown)
      isHeroImageVisible.value = false
      galleryVisibility.value = {}
      galleryItemRefs.value = []
      await nextTick()
      setupHeroImageObserver()
      setupGalleryObserver()
    } else {
      document.body.style.overflow = ''
      document.removeEventListener('keydown', handleKeyDown)
      cleanupObservers()
      isHeroImageVisible.value = false
      galleryVisibility.value = {}
      galleryItemRefs.value = []
    }
  }
)

watch(
  () => props.project?.images,
  async () => {
    if (!props.isOpen) return
    galleryVisibility.value = {}
    galleryItemRefs.value = []
    await nextTick()
    setupGalleryObserver()
  }
)

watch(
  () => props.project?.image,
  async () => {
    if (!props.isOpen) return
    isHeroImageVisible.value = false
    await nextTick()
    setupHeroImageObserver()
  }
)

const closeModal = () => {
  if (isImageModalOpen.value) {
    closeImageModal()
  }
  emit('close')
}

const closeImageModal = () => {
  isImageModalOpen.value = false
  selectedImage.value = null
  currentImageIndex.value = -1
}

const openImageModal = (image: string) => {
  selectedImage.value = image
  currentImageIndex.value = props.project?.images?.indexOf(image) ?? -1
  isImageModalOpen.value = true
}

const goToPreviousImage = () => {
  if (canGoToPrevious.value && props.project?.images) {
    slideDirection.value = 'left'
    currentImageIndex.value--
    selectedImage.value = props.project.images[currentImageIndex.value] ?? null
  }
}

const goToNextImage = () => {
  if (canGoToNext.value && props.project?.images) {
    slideDirection.value = 'right'
    currentImageIndex.value++
    selectedImage.value = props.project.images[currentImageIndex.value] ?? null
  }
}

const getStatusClass = (status: string) => {
  switch (status.toLowerCase()) {
    case 'terminé':
    case 'completed':
      return 'bg-green-500/20 text-green-300'
    case 'en développement':
    case 'in development':
      return 'bg-yellow-500/20 text-yellow-300'
    case 'en cours':
    case 'in progress':
      return 'bg-blue-500/20 text-blue-300'
    default:
      return 'bg-gray-500/20 text-gray-300'
  }
}
</script>

<style scoped>
.modal-enter-active,
.modal-leave-active {
  transition: opacity 0.3s ease;
}

.modal-enter-from,
.modal-leave-to {
  opacity: 0;
}

.modal-enter-active .modal-content,
.modal-leave-active .modal-content {
  transition: transform 0.3s ease;
}

.modal-enter-from .modal-content,
.modal-leave-to .modal-content {
  transform: scale(0.9) translateY(20px);
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}

/* Slide animations for image gallery navigation */
.slide-right-enter-active,
.slide-right-leave-active,
.slide-left-enter-active,
.slide-left-leave-active {
  transition: all 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
}

/* Slide right: new image comes from right, old goes to left */
.slide-right-enter-from {
  opacity: 0;
  transform: translateX(100%);
}

.slide-right-leave-to {
  opacity: 0;
  transform: translateX(-100%);
}

/* Slide left: new image comes from left, old goes to right */
.slide-left-enter-from {
  opacity: 0;
  transform: translateX(-100%);
}

.slide-left-leave-to {
  opacity: 0;
  transform: translateX(100%);
}
</style>
