import { defineStore } from 'pinia'
import { ref, computed } from 'vue'
import type { Experience } from '@/types'
import { useLocalizedData } from '@/composables/useLocalizedData'

export const useExperienceStore = defineStore('experience', () => {
  const { createLocalizedRef } = useLocalizedData()

  // Utiliser createLocalizedRef pour avoir des données réactives
  const rawExperiences = createLocalizedRef<Experience[]>('experience', [])
  const loading = ref(false)
  const error = ref<string | null>(null)

  // Computed pour trier automatiquement les expériences
  const experiences = computed(() => {
    // Prioritize ongoing experiences (oldest start first), then past ones by most recent end date
    return [...rawExperiences.value].sort((a, b) => {
      const isCurrentA = Boolean(a.current)
      const isCurrentB = Boolean(b.current)

      if (isCurrentA && isCurrentB) {
        const startA = new Date(a.startDate).getTime()
        const startB = new Date(b.startDate).getTime()
        return startA - startB
      }

      if (isCurrentA) return -1
      if (isCurrentB) return 1

      const endA = a.endDate ? new Date(a.endDate).getTime() : new Date(a.startDate).getTime()
      const endB = b.endDate ? new Date(b.endDate).getTime() : new Date(b.startDate).getTime()

      if (endA === endB) {
        const startA = new Date(a.startDate).getTime()
        const startB = new Date(b.startDate).getTime()
        return startB - startA
      }

      return endB - endA
    })
  })

  // Actions
  const getCurrentExperience = () => {
    return experiences.value.find((exp) => exp.current)
  }

  const getExperiencesByType = (type: Experience['type']) => {
    return experiences.value.filter((exp) => exp.type === type)
  }

  const getTotalYearsOfExperience = () => {
    let totalMonths = 0

    experiences.value.forEach((exp) => {
      const start = new Date(exp.startDate)
      const end = exp.endDate ? new Date(exp.endDate) : new Date()

      const monthsDiff =
        (end.getFullYear() - start.getFullYear()) * 12 + (end.getMonth() - start.getMonth())

      totalMonths += monthsDiff
    })

    return Math.floor(totalMonths / 12)
  }

  return {
    // State
    experiences,
    loading,
    error,

    // Actions
    getCurrentExperience,
    getExperiencesByType,
    getTotalYearsOfExperience,
  }
})
