<template>
  <section ref="sectionElement" v-bind="$attrs" :class="['py-16 md:py-24', className]">
    <AppContainer :class="containerClass">
      <slot />
    </AppContainer>
  </section>
</template>

<script setup lang="ts">
import { ref } from 'vue'
import AppContainer from './AppContainer.vue'

defineOptions({
  inheritAttrs: false,
})

const { className = '', containerClass = '' } = defineProps<{
  className?: string
  containerClass?: string
}>()

const sectionElement = ref<HTMLElement>()

// Expose the DOM element for refs
defineExpose({
  $el: sectionElement,
})
</script>
