import { ref, onMounted, onUnmounted, nextTick } from 'vue'
import { gsap } from 'gsap'
import type { ComponentPublicInstance } from 'vue'

export interface SectionRevealOptions {
  threshold?: number
  rootMargin?: string
  animation?:
    | 'fadeIn'
    | 'slideUp'
    | 'slideDown'
    | 'slideLeft'
    | 'slideRight'
    | 'scaleUp'
    | 'rotateIn'
  duration?: number
  delay?: number
  easing?: string
  once?: boolean
}

export function useSectionReveal(options: SectionRevealOptions = {}) {
  const {
    threshold = 0.1,
    rootMargin = '0px 0px -50px 0px',
    animation = 'slideUp',
    duration = 800,
    delay = 0,
    easing = 'cubic-bezier(0.16, 1, 0.3, 1)',
    once = true,
  } = options

  const sectionRef = ref<HTMLElement | null>(null)
  const isVisible = ref(false)
  const hasRevealed = ref(false)

  let observer: IntersectionObserver | null = null
  let animationInstance: GSAPTween | null = null

  // Helper pour résoudre l'élément DOM depuis la ref
  const resolveElement = (): Element | null => {
    if (!sectionRef.value) return null

    // Si c'est un composant Vue avec $el exposé
    if (typeof sectionRef.value === 'object' && '$el' in sectionRef.value) {
      return (sectionRef.value as ComponentPublicInstance<HTMLElement>).$el
    }
    // Si c'est directement un élément DOM
    else if (sectionRef.value instanceof Element) {
      return sectionRef.value
    }

    return null
  }

  // Animations prédéfinies avec GSAP
  const getAnimationPreset = (animationType: string) => {
    const animations = {
      fadeIn: {
        from: { opacity: 0 },
        to: { opacity: 1 },
      },
      slideUp: {
        from: { opacity: 0, y: 60 },
        to: { opacity: 1, y: 0 },
      },
      slideDown: {
        from: { opacity: 0, y: -60 },
        to: { opacity: 1, y: 0 },
      },
      slideLeft: {
        from: { opacity: 0, x: 60 },
        to: { opacity: 1, x: 0 },
      },
      slideRight: {
        from: { opacity: 0, x: -60 },
        to: { opacity: 1, x: 0 },
      },
      scaleUp: {
        from: { opacity: 0, scale: 0.8 },
        to: { opacity: 1, scale: 1 },
      },
      rotateIn: {
        from: { opacity: 0, rotationX: -15, transformPerspective: 1000 },
        to: { opacity: 1, rotationX: 0, transformPerspective: 1000 },
      },
    }
    return animations[animationType as keyof typeof animations] || animations.slideUp
  }

  // Appliquer les styles initiaux avec GSAP
  const applyInitialStyles = () => {
    const element = resolveElement()
    if (!element) return

    const { from } = getAnimationPreset(animation)

    // Appliquer les styles initiaux avec GSAP (sans animation)
    gsap.set(element, from)
  }

  // Révéler la section avec animation GSAP
  const revealSection = () => {
    const element = resolveElement()
    if (!element || hasRevealed.value) return

    const { to } = getAnimationPreset(animation)

    // Créer l'animation avec GSAP
    animationInstance = gsap.to(element, {
      ...to,
      duration: duration / 1000,
      delay: delay / 1000,
      ease: easing.replace('cubic-bezier', 'power2.out'),
      onComplete: () => {
        hasRevealed.value = true
        isVisible.value = true
      },
    })
  }

  // Masquer la section avec GSAP (si once = false)
  const hideSection = () => {
    const element = resolveElement()
    if (!element || once) return

    const { from } = getAnimationPreset(animation)

    // Arrêter l'animation en cours si elle existe
    if (animationInstance) {
      animationInstance.kill()
      animationInstance = null
    }

    // Remettre aux styles initiaux
    gsap.set(element, from)
    hasRevealed.value = false
    isVisible.value = false
  }

  // Créer l'observateur
  const createObserver = () => {
    if (!('IntersectionObserver' in window)) {
      // Fallback pour les navigateurs sans support
      revealSection()
      return
    }

    observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            revealSection()

            // Arrêter d'observer si once = true
            if (once && observer) {
              observer.unobserve(entry.target)
            }
          } else if (!once) {
            hideSection()
          }
        })
      },
      {
        threshold,
        rootMargin,
      }
    )
  }

  // Démarrer l'observation
  const startObserving = () => {
    if (!observer || !sectionRef.value) {
      return
    }

    // Résoudre l'élément DOM depuis la ref
    let elementToObserve: Element | null = null

    // Si c'est un composant Vue avec $el exposé
    if (sectionRef.value && typeof sectionRef.value === 'object' && '$el' in sectionRef.value) {
      elementToObserve = (sectionRef.value as ComponentPublicInstance<HTMLElement>).$el
    }
    // Si c'est directement un élément DOM
    else if (sectionRef.value instanceof Element) {
      elementToObserve = sectionRef.value
    }

    if (!elementToObserve) return

    applyInitialStyles()
    observer.observe(elementToObserve)
  }

  // Arrêter l'observation et nettoyer les animations
  const stopObserving = () => {
    if (observer) {
      observer.disconnect()
      observer = null
    }
    if (animationInstance) {
      animationInstance.kill()
      animationInstance = null
    }
  }

  // Vérifier la préférence de mouvement réduit
  const prefersReducedMotion = () => {
    return window.matchMedia('(prefers-reduced-motion: reduce)').matches
  }

  onMounted(() => {
    // Ignorer les animations si l'utilisateur préfère un mouvement réduit
    if (prefersReducedMotion()) {
      const element = resolveElement()
      if (element) {
        gsap.set(element, { opacity: 1, x: 0, y: 0, scale: 1, rotation: 0 })
      }
      isVisible.value = true
      hasRevealed.value = true
      return
    }

    createObserver()

    // Démarrer l'observation quand l'élément est disponible
    nextTick(() => {
      if (sectionRef.value) {
        startObserving()
      }
    })
  })

  onUnmounted(() => {
    stopObserving()
  })

  return {
    sectionRef,
    isVisible,
    hasRevealed,
    startObserving,
    stopObserving,
    revealSection: () => revealSection(),
    hideSection: () => hideSection(),
  }
}
