import { defineStore } from 'pinia'
import { ref } from 'vue'
import type { SkillCategory } from '@/types'
import { useLocalizedData } from '@/composables/useLocalizedData'

export const useSkillsStore = defineStore('skills', () => {
  const { createLocalizedRef } = useLocalizedData()

  // Utiliser createLocalizedRef pour avoir des données réactives
  const skills = createLocalizedRef<SkillCategory[]>('skills', [])
  const loading = ref(false)
  const error = ref<string | null>(null)

  // Actions
  const getSkillsByCategory = (category: string) => {
    return skills.value.find((skillGroup) => skillGroup.category === category)
  }

  const getAllSkills = () => {
    return skills.value.flatMap((category) => category.skills)
  }

  return {
    // State
    skills,
    loading,
    error,

    // Actions
    getSkillsByCategory,
    getAllSkills,
  }
})
