import { defineStore } from 'pinia'
import { reactive } from 'vue'
import type { ContactFormData, ContactFormState, ContactFormErrors } from '@/types'
import { z } from 'zod'
import emailjs from '@emailjs/browser'

// Schéma de validation Zod
const contactSchema = z.object({
  name: z
    .string()
    .min(2, 'Le nom doit contenir au moins 2 caractères')
    .max(100, 'Le nom ne peut pas dépasser 100 caractères'),
  email: z
    .string()
    .email('Adresse email invalide')
    .max(255, "L'email ne peut pas dépasser 255 caractères"),
  subject: z
    .string()
    .min(1, 'Veuillez sélectionner un sujet')
    .max(100, 'Le sujet ne peut pas dépasser 100 caractères'),
  message: z
    .string()
    .min(20, 'Le message doit contenir au moins 20 caractères')
    .max(2000, 'Le message ne peut pas dépasser 2000 caractères'),
  consent: z.boolean().refine((val) => val === true, 'Vous devez accepter les conditions'),
  honeypot: z.string().optional(),
})

export const useContactStore = defineStore('contact', () => {
  const formState = reactive<ContactFormState>({
    data: {
      name: '',
      email: '',
      subject: '',
      message: '',
      consent: false,
      honeypot: '',
    },
    errors: {},
    loading: false,
    success: false,
  })

  // Actions
  const validateForm = (): boolean => {
    try {
      formState.errors = {}

      // Vérification anti-spam (honeypot)
      if (formState.data.honeypot) {
        throw new Error('Spam détecté')
      }

      contactSchema.parse(formState.data)
      return true
    } catch (error) {
      if (error instanceof z.ZodError) {
        error.errors.forEach((err) => {
          const field = err.path[0] as keyof ContactFormErrors
          formState.errors[field] = err.message
        })
      } else {
        formState.errors.general = error instanceof Error ? error.message : 'Erreur de validation'
      }
      return false
    }
  }

  const submitForm = async (): Promise<boolean> => {
    if (!validateForm()) {
      return false
    }

    try {
      formState.loading = true
      formState.errors = {}

      // Configuration EmailJS (à remplacer par tes vraies clés)
      const serviceId = import.meta.env.VITE_EMAILJS_SERVICE_ID || 'your_service_id'
      const templateId = import.meta.env.VITE_EMAILJS_TEMPLATE_ID || 'your_template_id'
      const publicKey = import.meta.env.VITE_EMAILJS_PUBLIC_KEY || 'your_public_key'

      // Préparer les données pour EmailJS
      const templateParams = {
        from_name: formState.data.name,
        from_email: formState.data.email,
        subject: formState.data.subject,
        message: formState.data.message,
        time: new Date().toLocaleString('fr-FR'), // Date et heure actuelles
      }

      // Envoyer l'email via EmailJS
      await emailjs.send(serviceId, templateId, templateParams, publicKey)

      formState.success = true

      // Réinitialiser le formulaire après succès
      setTimeout(() => {
        resetForm()
      }, 3000)

      return true
    } catch (error) {
      console.error('Erreur EmailJS:', error)
      formState.errors.general =
        error instanceof Error
          ? `Erreur lors de l'envoi: ${error.message}`
          : "Une erreur est survenue lors de l'envoi. Veuillez réessayer."
      return false
    } finally {
      formState.loading = false
    }
  }

  const resetForm = () => {
    formState.data = {
      name: '',
      email: '',
      subject: '',
      message: '',
      consent: false,
      honeypot: '',
    }
    formState.errors = {}
    formState.loading = false
    formState.success = false
  }

  const clearErrors = () => {
    formState.errors = {}
  }

  const clearFieldError = (field: keyof ContactFormErrors) => {
    delete formState.errors[field]
  }

  const setFieldValue = <K extends keyof ContactFormData>(field: K, value: ContactFormData[K]) => {
    formState.data[field] = value

    // Nettoyer l'erreur du champ lors de la saisie
    if (formState.errors[field as keyof ContactFormErrors]) {
      delete formState.errors[field as keyof ContactFormErrors]
    }
  }

  return {
    // State
    formState,

    // Actions
    validateForm,
    submitForm,
    resetForm,
    clearErrors,
    clearFieldError,
    setFieldValue,
  }
})
