import { defineStore } from 'pinia'
import { ref, computed } from 'vue'
import { useLocalizedData } from '@/composables/useLocalizedData'

export interface Testimonial {
  id: string
  name: string
  role: string
  company: string
  companyLogo?: string
  avatar: string
  rating: number
  content: string
  date: string
  featured: boolean
  tags: string[]
}

export const useTestimonialsStore = defineStore('testimonials', () => {
  const { createLocalizedRef } = useLocalizedData()

  // Utiliser createLocalizedRef pour avoir des données réactives
  const testimonials = createLocalizedRef<Testimonial[]>('testimonials', [])
  const loading = ref(false)
  const error = ref<string | null>(null)

  // Computed
  const featuredTestimonials = computed(() => testimonials.value.filter((t) => t.featured))

  const sortedTestimonials = computed(() =>
    [...testimonials.value].sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime())
  )

  const averageRating = computed(() => {
    if (testimonials.value.length === 0) return 0
    const sum = testimonials.value.reduce((acc, t) => acc + t.rating, 0)
    return Math.round((sum / testimonials.value.length) * 10) / 10
  })

  // Actions
  const getTestimonialById = (id: string) => {
    return testimonials.value.find((t) => t.id === id)
  }

  const getTestimonialsByTag = (tag: string) => {
    return testimonials.value.filter((t) =>
      t.tags.some((tTag) => tTag.toLowerCase().includes(tag.toLowerCase()))
    )
  }

  return {
    // State
    testimonials,
    loading,
    error,

    // Computed
    featuredTestimonials,
    sortedTestimonials,
    averageRating,

    // Actions
    getTestimonialById,
    getTestimonialsByTag,
  }
})
