<template>
  <AppSection
    ref="sectionRef"
    id="projects"
    class="relative overflow-hidden bg-white dark:bg-gray-900"
  >
    <!-- Background animé -->
    <div class="absolute inset-0 opacity-20">
      <div
        class="animate-float-slow absolute left-1/3 top-1/3 h-96 w-96 rounded-full bg-purple-500/10 blur-3xl"
      ></div>
      <div
        class="animate-float-reverse absolute bottom-1/3 right-1/3 h-96 w-96 rounded-full bg-blue-500/10 blur-3xl"
      ></div>
    </div>

    <div class="relative">
      <!-- En-tête de section -->
      <div class="mb-16 text-center">
        <h2
          class="mb-4 bg-gradient-to-r from-purple-600 via-blue-600 to-cyan-600 bg-clip-text text-3xl font-bold text-transparent md:text-4xl"
        >
          {{ tUI('projects.title') }}
        </h2>
        <p class="mx-auto mb-6 max-w-3xl text-lg text-gray-600 dark:text-gray-300">
          {{ tUI('projects.subtitle') }}
        </p>
        <!-- Ligne de séparation animée -->
        <div
          class="mx-auto h-1 w-32 rounded-full bg-gradient-to-r from-purple-500 to-cyan-500"
        ></div>
      </div>
      <!-- Statistiques des projets -->
      <div v-if="!loading && featuredProjects.length > 0">
        <div class="mb-12 grid grid-cols-2 gap-6 md:grid-cols-3">
          <div
            class="rounded-xl border border-gray-200 bg-white/50 p-4 text-center backdrop-blur-sm transition-all duration-300 hover:scale-105 hover:bg-white/70 dark:border-gray-700 dark:bg-gray-900/50 dark:hover:bg-gray-900/70"
          >
            <div class="mb-1 text-2xl font-bold text-purple-600">{{ totalProjects }}</div>
            <div class="text-sm text-gray-600 dark:text-gray-400">
              {{ tUI('projects.projectsLabel') }}
            </div>
          </div>
          <div
            class="rounded-xl border border-gray-200 bg-white/50 p-4 text-center backdrop-blur-sm transition-all duration-300 hover:scale-105 hover:bg-white/70 dark:border-gray-700 dark:bg-gray-900/50 dark:hover:bg-gray-900/70"
          >
            <div class="mb-1 text-2xl font-bold text-blue-600">{{ completedProjects }}</div>
            <div class="text-sm text-gray-600 dark:text-gray-400">
              {{ tUI('projects.completedLabel') }}
            </div>
          </div>
          <div
            class="rounded-xl border border-gray-200 bg-white/50 p-4 text-center backdrop-blur-sm transition-all duration-300 hover:scale-105 hover:bg-white/70 dark:border-gray-700 dark:bg-gray-900/50 dark:hover:bg-gray-900/70"
          >
            <div class="mb-1 text-2xl font-bold text-green-600">{{ totalTechnologies }}</div>
            <div class="text-sm text-gray-600 dark:text-gray-400">
              {{ tUI('projects.technologiesLabel') }}
            </div>
          </div>
        </div>
      </div>

      <!-- Grille des projets -->
      <div v-if="!loading && featuredProjects.length > 0">
        <Transition name="fade" mode="out-in">
          <div
            :key="showAllProjects ? 'all' : 'featured'"
            class="mb-12 grid grid-cols-1 gap-8 md:grid-cols-2 lg:grid-cols-3"
          >
            <UiProjectCard
              v-for="(project, index) in displayedProjectCards"
              :key="project.id"
              :project="project"
              :variant="index === 0 ? 'featured' : 'default'"
              :style="{ animationDelay: `${index * 0.15}s` }"
              class="project-card-animated"
              @click="openProjectModal(project)"
              @demo="handleProjectDemo"
              @github="handleProjectGithub"
              @details="openProjectModal(project)"
              @figma="handleProjectFigma"
            />
          </div>
        </Transition>
      </div>

      <!-- État de chargement avec animation -->
      <div v-else-if="loading">
        <div class="mb-12 grid grid-cols-1 gap-8 md:grid-cols-2 lg:grid-cols-3">
          <div
            v-for="i in 6"
            :key="i"
            class="relative h-80 animate-pulse overflow-hidden rounded-2xl bg-gray-200 dark:bg-gray-800"
          >
            <div
              class="animate-shimmer absolute inset-0 -translate-x-full bg-gradient-to-r from-transparent via-white/10 to-transparent"
            ></div>
            <div class="p-6">
              <div class="mb-2 h-4 w-3/4 rounded bg-gray-300 dark:bg-gray-700"></div>
              <div class="mb-4 h-3 w-1/2 rounded bg-gray-300 dark:bg-gray-700"></div>
              <div class="space-y-2">
                <div class="h-3 rounded bg-gray-300 dark:bg-gray-700"></div>
                <div class="h-3 w-5/6 rounded bg-gray-300 dark:bg-gray-700"></div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- Aucun projet -->
      <div v-else class="py-12 text-center">
        <div class="relative mb-6">
          <div
            class="absolute inset-0 rounded-full bg-gradient-to-r from-gray-400 to-gray-500 opacity-20 blur"
          ></div>
          <FontAwesomeIcon icon="folder-open" class="relative text-6xl text-gray-400" />
        </div>
        <p class="mb-4 text-lg text-gray-600 dark:text-gray-300">
          Aucun projet en vedette pour le moment.
        </p>
        <p class="text-sm text-gray-500 dark:text-gray-400">
          De nouveaux projets arrivent bientôt !
        </p>
      </div>

      <!-- CTA pour voir tous les projets -->
      <div v-if="shouldShowMoreButton" class="space-y-4 text-center">
        <UiButton
          variant="outline"
          size="lg"
          class="group relative overflow-hidden border-purple-200 bg-white/80 backdrop-blur-sm hover:border-purple-300 dark:border-purple-800 dark:bg-gray-900/80 dark:hover:border-purple-700"
          @click="toggleProjectsDisplay"
        >
          <div
            class="absolute inset-0 bg-gradient-to-r from-purple-500 to-cyan-500 opacity-0 transition-opacity duration-300 group-hover:opacity-10"
          ></div>
          <span class="relative flex items-center">
            <FontAwesomeIcon
              :icon="showAllProjects ? 'arrow-up' : 'th-large'"
              class="mr-2 transition-transform duration-300 group-hover:rotate-12"
            />
            {{ showAllProjects ? tUI('projects.viewLess') : tUI('projects.viewMore') }}
            <FontAwesomeIcon
              :icon="showAllProjects ? 'arrow-up' : 'arrow-down'"
              class="ml-2 transition-transform duration-300 group-hover:translate-x-1"
            />
          </span>
        </UiButton>

        <!-- GitHub Link when showing all projects -->
        <Transition name="fade">
          <div v-if="showAllProjects" class="mt-4">
            <button
              @click="goToGithub"
              class="inline-flex items-center rounded-lg px-4 py-2 text-sm font-medium text-purple-600 transition-colors duration-200 hover:bg-purple-50 hover:text-purple-700 dark:text-purple-400 dark:hover:bg-purple-900/20 dark:hover:text-purple-300"
            >
              <FontAwesomeIcon icon="fab fa-github" class="mr-2 h-4 w-4" />
              {{ tUI('projects.allMyProjects') }}
              <FontAwesomeIcon icon="external-link-alt" class="ml-1 h-3 w-3" />
            </button>
          </div>
        </Transition>
      </div>
    </div>

    <!-- Modal de détails du projet -->
    <ProjectModal :is-open="isModalOpen" :project="selectedProject" @close="closeProjectModal" />
  </AppSection>
</template>

<script setup lang="ts">
import { computed, ref } from 'vue'
import { useProjectsStore } from '@/stores/projects'
import { useSectionReveal } from '@/composables/useSectionReveal'
import { useI18n } from '@/composables/useI18n'
import { useSocialLinksStore } from '@/stores/social-links'
import AppSection from '@/components/layout/AppSection.vue'
import UiButton from '@/components/ui/UiButton.vue'
import UiProjectCard from '@/components/ui/UiProjectCard.vue'
import ProjectModal from '@/components/ui/ProjectModal.vue'

const projectsStore = useProjectsStore()
const { tUI } = useI18n()
const socialLinksStore = useSocialLinksStore()

// État local pour l'affichage des projets
const showAllProjects = ref(false)

// État du modal
const isModalOpen = ref(false)
const selectedProject = ref<Project | null>(null)

// Animation d'apparition de la section
const { sectionRef } = useSectionReveal({
  animation: 'fadeIn',
  duration: 800,
  delay: 100,
})

const featuredProjects = computed(() => projectsStore.featuredProjects)
const allProjects = computed(() => projectsStore.projects)
const loading = computed(() => projectsStore.loading)

// Vérifier s'il faut afficher le bouton "Voir plus"
const shouldShowMoreButton = computed(() => allProjects.value.length > 3)

// Projets à afficher (featured ou tous selon l'état)
const displayedProjects = computed(() =>
  showAllProjects.value ? allProjects.value : featuredProjects.value
)

const displayedProjectCards = computed(() =>
  displayedProjects.value.map((project) => {
    const card = { ...project } as ProjectCard
    if (project.links?.demo) {
      card.demoUrl = project.links.demo
    }
    if (project.links?.github) {
      card.githubUrl = project.links.github
    }
    if (project.links?.figma) {
      card.figmaUrl = project.links.figma
    }
    return card
  })
)

// Lien GitHub dynamique
const githubUrl = computed(
  () =>
    socialLinksStore.enabledSocialLinks.find((link) => link.key === 'github')?.url ??
    'https://github.com'
)

// Fonction pour basculer l'affichage des projets
const toggleProjectsDisplay = () => {
  showAllProjects.value = !showAllProjects.value
}

// Fonction pour aller vers GitHub
const goToGithub = () => {
  window.open(githubUrl.value, '_blank')
}

// Fonctions de gestion du modal
const openProjectModal = (project: unknown) => {
  selectedProject.value = project as Project
  isModalOpen.value = true
}

const closeProjectModal = () => {
  isModalOpen.value = false
  selectedProject.value = null
}

// Statistiques calculées
const totalProjects = computed(() => allProjects.value.length)

const completedProjects = computed(
  () =>
    allProjects.value.filter(
      (project) => project.status === 'Terminé' || project.status === 'Completed'
    ).length
)

const totalTechnologies = computed(() => {
  const allTechs = allProjects.value.flatMap((project) => project.technologies)
  return new Set(allTechs).size
})

// Définition du type local pour correspondre à UiProjectCard
interface ProjectCard {
  id: string
  title: string
  slug?: string
  description: string
  image: string
  technologies: string[]
  status: string
  demoUrl?: string
  githubUrl?: string
  figmaUrl?: string
  year?: number | string
  links?: {
    demo?: string | null
    github?: string | null
    case_study?: string | null
    figma?: string | null
  }
}

// Définition du type Project pour le modal
interface Project {
  id: string
  title: string
  description: string
  longDescription: string
  image: string
  images?: string[]
  technologies: string[]
  category: string
  status: string
  year: string
  duration: string
  team: string
  role: string
  challenges?: string[]
  solutions?: string[]
  results?: string[]
  links: {
    demo?: string
    github?: string
    case_study?: string
    figma?: string
  }
  metrics?: {
    performance: number
    accessibility: number
    seo: number
    best_practices: number
  }
}

// Gestionnaires d'événements restants
const handleProjectDemo = (project: ProjectCard) => {
  if (project.demoUrl) {
    window.open(project.demoUrl, '_blank')
  }
}

const handleProjectGithub = (project: ProjectCard) => {
  if (project.githubUrl) {
    window.open(project.githubUrl, '_blank')
  }
}

const handleProjectFigma = (project: ProjectCard) => {
  if (project.figmaUrl) {
    window.open(project.figmaUrl, '_blank')
  }
}
</script>

