<template>
  <div class="pt-16">
    <!-- Section Hero -->
    <HeroSection />

    <!-- Section Projets -->
    <ProjectsSection />

    <!-- Section Compétences -->
    <SkillsSection />

    <!-- Section Expérience -->
    <ExperienceSection />

    <!-- Section Témoignages -->
    <TestimonialsSection />

    <!-- Section À propos -->
    <AboutSection />

    <!-- Section Contact -->
    <ContactSection />
  </div>
</template>

<script setup lang="ts">
import HeroSection from '@/features/hero/HeroSection.vue'
import ProjectsSection from '@/features/projects/ProjectsSection.vue'
import SkillsSection from '@/features/skills/SkillsSection.vue'
import ExperienceSection from '@/features/experience/ExperienceSection.vue'
import TestimonialsSection from '@/features/testimonials/TestimonialsSection.vue'
import AboutSection from '@/features/about/AboutSection.vue'
import ContactSection from '@/features/contact/ContactSection.vue'
import { useLocalizedData } from '@/composables/useLocalizedData'
import { onMounted, ref, computed, watch } from 'vue'
import { useI18n } from 'vue-i18n'

// SEO
import { useHead } from '@unhead/vue'

const { loadHeroData } = useLocalizedData()
const { locale } = useI18n()
const heroData = ref<unknown>(null)

async function loadData() {
  heroData.value = await loadHeroData()
}

onMounted(async () => {
  await loadData()
})

// Reload when language changes
watch(locale, async () => {
  await loadData()
})

// Computed pour les meta tags
const headMeta = computed(() => {
  if (!heroData.value) return {}

  const data = heroData.value as Record<string, Record<string, string>>
  const personal = data.personal
  if (!personal) return {}

  const fullName = `${personal.name} ${personal.lastName}`

  return {
    title: `Accueil | Portfolio ${fullName}`,
    meta: [
      {
        name: 'description',
        content: `Portfolio professionnel de ${fullName}, ${(personal.description || '').replace(/<[^>]*>/g, '')}. ${personal.subtitle || ''}`,
      },
      {
        name: 'keywords',
        content:
          'développeur full stack, vue.js, typescript, node.js, portfolio, projets web, développement frontend, backend',
      },
      { property: 'og:title', content: `${fullName} - Développeur Full Stack` },
      { property: 'og:description', content: personal.subtitle || '' },
      { property: 'og:type', content: 'website' },
    ],
  }
})

// Utiliser useHead avec le computed
useHead(headMeta)
</script>
