<template>
  <div :class="cardClasses">
    <div v-if="$slots.header || title" class="p-6 pb-4">
      <slot name="header">
        <h3 v-if="title" class="text-lg font-semibold text-gray-900 dark:text-white">
          {{ title }}
        </h3>
      </slot>
    </div>

    <div v-if="$slots.default" :class="contentClasses">
      <slot />
    </div>

    <div v-if="$slots.footer" class="border-t border-gray-200 p-6 pt-4 dark:border-gray-700">
      <slot name="footer" />
    </div>
  </div>
</template>

<script setup lang="ts">
import { computed } from 'vue'

interface Props {
  title?: string
  variant?: 'default' | 'bordered' | 'shadow' | 'ghost'
  padding?: boolean
  hover?: boolean
  clickable?: boolean
}

const props = withDefaults(defineProps<Props>(), {
  variant: 'default',
  padding: true,
  hover: false,
  clickable: false,
})

const cardClasses = computed(() => {
  const baseClasses = ['bg-white dark:bg-gray-800', 'transition-all duration-200']

  const variantClasses = {
    default: ['rounded-xl'],
    bordered: ['rounded-xl border border-gray-200 dark:border-gray-700'],
    shadow: ['rounded-xl shadow-md hover:shadow-lg'],
    ghost: ['rounded-xl bg-transparent'],
  }

  const interactionClasses = []

  if (props.hover) {
    interactionClasses.push('hover:shadow-lg hover:-translate-y-1')
  }

  if (props.clickable) {
    interactionClasses.push(
      'cursor-pointer focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2'
    )
  }

  return [...baseClasses, ...variantClasses[props.variant], ...interactionClasses]
    .filter(Boolean)
    .join(' ')
})

const contentClasses = computed(() => {
  return props.padding ? 'p-6' : ''
})
</script>
