<template>
  <AppSection ref="sectionRef" id="about" class="bg-gray-50 dark:bg-gray-800">
    <div class="grid grid-cols-1 items-center gap-16 lg:grid-cols-2">
      <!-- Image -->
      <div class="order-2 lg:order-1">
        <div class="relative">
          <!-- Décoration -->
          <div
            class="absolute -bottom-4 -right-4 h-32 w-32 rounded-2xl bg-blue-600 dark:bg-blue-500"
          ></div>
          <div class="absolute -left-4 -top-4 h-20 w-20 rounded-full bg-yellow-400"></div>
          <div
            class="relative flex h-96 w-full items-center justify-center rounded-2xl bg-gradient-to-br from-blue-500 via-purple-600 to-indigo-700 shadow-2xl"
          >
            <Avatar
              :src="avatarImage"
              :alt="aboutAvatar.alt"
              :name="aboutAvatar.name"
              class="h-80 w-80"
              rounded="lg"
            />
          </div>
        </div>
      </div>

      <!-- Contenu -->
      <div class="order-1 lg:order-2">
        <h2 class="mb-6 text-3xl font-bold text-gray-900 dark:text-white md:text-4xl">
          {{ aboutContent.title }}
        </h2>

        <div class="prose prose-lg mb-8 text-gray-600 dark:text-gray-300">
          <p
            v-for="(paragraph, index) in aboutParagraphs"
            :key="`paragraph-${index}`"
            :class="['mb-4', { 'mb-6': index === aboutParagraphs.length - 1 }]"
            v-html="paragraph"
          ></p>
        </div>

        <!-- Valeurs -->
        <div class="mb-8 space-y-4">
          <h3 class="text-xl font-bold text-gray-900 dark:text-white">
            {{ aboutContent.valuesTitle }}
          </h3>
          <div class="grid grid-cols-1 gap-4 md:grid-cols-2">
            <div
              v-for="(value, index) in aboutValues"
              :key="`value-${index}`"
              class="flex items-center"
            >
              <FontAwesomeIcon
                :icon="value.icon"
                class="mr-3"
                :class="value.color || 'text-blue-600 dark:text-blue-400'"
              />
              <span class="text-gray-700 dark:text-gray-300">{{ value.text }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </AppSection>
</template>

<script setup lang="ts">
import { computed } from 'vue'
import { useLocalizedData } from '@/composables/useLocalizedData'
import { useSectionReveal } from '@/composables/useSectionReveal'
import AppSection from '@/components/layout/AppSection.vue'
import Avatar from '@/components/common/Avatar.vue'

// L'avatar utilise import.meta.env.BASE_URL pour traiter le base correct
const avatarImage = import.meta.env.BASE_URL + 'images/avatars/avatar.webp'

interface AboutValue {
  icon: string
  text: string
  color?: string
}

interface AboutAvatar {
  alt: string
  name: string
}

interface AboutContent {
  title: string
  description?: string
  valuesTitle?: string
  paragraphs: string[]
  values: AboutValue[]
  avatar: AboutAvatar
}

const defaultAboutContent: AboutContent = {
  title: '',
  description: '',
  valuesTitle: '',
  paragraphs: [],
  values: [],
  avatar: {
    alt: '',
    name: '',
  },
}

const { createLocalizedRef } = useLocalizedData()
const aboutContentRef = createLocalizedRef<AboutContent>('about', defaultAboutContent)

const aboutContent = computed(() => ({
  ...defaultAboutContent,
  ...(aboutContentRef.value ?? {}),
}))

const aboutParagraphs = computed(() => aboutContent.value.paragraphs ?? [])
const aboutValues = computed(() => aboutContent.value.values ?? [])
const aboutAvatar = computed(() => aboutContent.value.avatar ?? defaultAboutContent.avatar)

// Animation d'apparition de la section
const { sectionRef } = useSectionReveal({
  animation: 'fadeIn',
  duration: 800,
  delay: 0,
})
</script>
