<template>
  <div class="flex items-center">
    <div
      v-for="n in maxRating"
      :key="n"
      @click="!readonly && setRating(n)"
      @mouseenter="!readonly && setHoverRating(n)"
      @mouseleave="!readonly && setHoverRating(0)"
      :class="[
        'cursor-pointer transition-colors duration-200',
        readonly ? 'cursor-default' : 'hover:scale-110',
        n <= (hoverRating || modelValue) ? 'text-yellow-400' : 'text-gray-300 dark:text-gray-600',
      ]"
    >
      <FontAwesomeIcon
        v-if="icon.startsWith('fas fa-')"
        :icon="icon.replace('fas fa-', '')"
        :class="sizeClass"
      />
      <FontAwesomeIcon
        v-else-if="icon.startsWith('fab fa-')"
        :icon="['fab', icon.replace('fab fa-', '')]"
        :class="sizeClass"
      />
      <i v-else :class="[iconClass, sizeClass]"></i>
    </div>
    <div v-if="showValue" class="ml-2 text-sm text-gray-600 dark:text-gray-400">
      {{ modelValue }}/{{ maxRating }}
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed } from 'vue'

export interface RatingProps {
  modelValue: number
  maxRating?: number
  readonly?: boolean
  showValue?: boolean
  size?: 'sm' | 'md' | 'lg'
  icon?: string
}

const props = withDefaults(defineProps<RatingProps>(), {
  maxRating: 5,
  readonly: false,
  showValue: false,
  size: 'md',
  icon: 'fas fa-star',
})

const { modelValue, maxRating, readonly, showValue, size, icon } = props

const emit = defineEmits<{
  (e: 'update:modelValue', value: number): void
}>()

const hoverRating = ref(0)

// Classes dynamiques
const sizeClass = computed(() => {
  const sizes = {
    sm: 'text-sm',
    md: 'text-base',
    lg: 'text-lg',
  }
  return sizes[size]
})

const iconClass = computed(() => icon)

// Méthodes
const setRating = (rating: number) => {
  if (!readonly) {
    emit('update:modelValue', rating)
  }
}

const setHoverRating = (rating: number) => {
  if (!readonly) {
    hoverRating.value = rating
  }
}
</script>
