<template>
  <UiCard :title="tUI('contact.getInTouch')">
    <form @submit.prevent="handleSubmit" class="space-y-6">
      <!-- Champ honeypot (caché) -->
      <input
        v-model="formState.data.honeypot"
        type="text"
        name="website"
        class="hidden"
        tabindex="-1"
        autocomplete="off"
      />

      <!-- Nom -->
      <div>
        <label for="name" class="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
          {{ tUI('contact.form.name') }} *
        </label>
        <input
          id="name"
          v-model="formState.data.name"
          type="text"
          name="name"
          required
          class="w-full rounded-lg border border-gray-300 bg-white px-4 py-3 text-gray-900 focus:border-transparent focus:ring-2 focus:ring-blue-500 dark:border-gray-600 dark:bg-gray-800 dark:text-white"
          :class="{ 'border-red-500': formState.errors.name }"
          :placeholder="tUI('contact.form.placeholders.name')"
          @input="clearFieldError('name')"
        />
        <p v-if="formState.errors.name" class="mt-1 text-sm text-red-600 dark:text-red-400">
          {{ formState.errors.name }}
        </p>
      </div>

      <!-- Email -->
      <div>
        <label for="email" class="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
          {{ tUI('contact.form.email') }} *
        </label>
        <input
          id="email"
          v-model="formState.data.email"
          type="email"
          name="email"
          required
          class="w-full rounded-lg border border-gray-300 bg-white px-4 py-3 text-gray-900 focus:border-transparent focus:ring-2 focus:ring-blue-500 dark:border-gray-600 dark:bg-gray-800 dark:text-white"
          :class="{ 'border-red-500': formState.errors.email }"
          :placeholder="tUI('contact.form.placeholders.email')"
          @input="clearFieldError('email')"
        />
        <p v-if="formState.errors.email" class="mt-1 text-sm text-red-600 dark:text-red-400">
          {{ formState.errors.email }}
        </p>
      </div>

      <!-- Sujet -->
      <div>
        <label
          for="subject"
          class="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300"
        >
          {{ tUI('contact.form.subject') }} *
        </label>
        <select
          id="subject"
          v-model="formState.data.subject"
          name="subject"
          required
          class="w-full rounded-lg border border-gray-300 bg-white px-4 py-3 text-gray-900 focus:border-transparent focus:ring-2 focus:ring-blue-500 dark:border-gray-600 dark:bg-gray-800 dark:text-white"
          :class="{ 'border-red-500': formState.errors.subject }"
          @change="clearFieldError('subject')"
        >
          <option value="">{{ tUI('contact.form.placeholders.subject') }}</option>
          <option v-for="option in subjectOptions" :key="option.value" :value="option.value">
            {{ option.label }}
          </option>
        </select>
        <p v-if="formState.errors.subject" class="mt-1 text-sm text-red-600 dark:text-red-400">
          {{ formState.errors.subject }}
        </p>
      </div>

      <!-- Message -->
      <div>
        <label
          for="message"
          class="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300"
        >
          {{ tUI('contact.form.message') }} *
        </label>
        <textarea
          id="message"
          v-model="formState.data.message"
          name="message"
          rows="5"
          required
          class="resize-vertical w-full rounded-lg border border-gray-300 bg-white px-4 py-3 text-gray-900 focus:border-transparent focus:ring-2 focus:ring-blue-500 dark:border-gray-600 dark:bg-gray-800 dark:text-white"
          :class="{ 'border-red-500': formState.errors.message }"
          :placeholder="tUI('contact.form.placeholders.message')"
          @input="clearFieldError('message')"
        ></textarea>
        <div class="mt-1 flex justify-between">
          <p v-if="formState.errors.message" class="text-sm text-red-600 dark:text-red-400">
            {{ formState.errors.message }}
          </p>
          <p class="ml-auto text-sm text-gray-500 dark:text-gray-400">
            {{ characterCountLabel }}
          </p>
        </div>
      </div>

      <!-- Consentement -->
      <div class="flex items-start">
        <input
          id="consent"
          v-model="formState.data.consent"
          type="checkbox"
          name="consent"
          required
          class="mt-1 h-4 w-4 rounded border-gray-300 bg-white text-blue-600 focus:ring-blue-500 dark:border-gray-600 dark:bg-gray-800"
          :class="{ 'border-red-500': formState.errors.consent }"
          @change="clearFieldError('consent')"
        />
        <label for="consent" class="ml-3 text-sm text-gray-600 dark:text-gray-300">
          {{ tUI('contact.form.consent') }} *
        </label>
      </div>
      <p v-if="formState.errors.consent" class="text-sm text-red-600 dark:text-red-400">
        {{ formState.errors.consent }}
      </p>

      <!-- Erreur générale -->
      <div
        v-if="formState.errors.general"
        class="rounded-lg border border-red-200 bg-red-50 p-4 dark:border-red-800 dark:bg-red-900/20"
      >
        <p class="text-sm text-red-600 dark:text-red-400">
          {{ formState.errors.general }}
        </p>
      </div>

      <!-- Message de succès -->
      <div
        v-if="formState.success"
        class="rounded-lg border border-green-200 bg-green-50 p-4 dark:border-green-800 dark:bg-green-900/20"
      >
        <div class="flex items-center">
          <FontAwesomeIcon icon="check-circle" class="mr-3 text-green-600 dark:text-green-400" />
          <div>
            <p class="text-sm font-semibold text-green-600 dark:text-green-400">
              {{ tUI('contact.form.success') }}
            </p>
            <p class="text-sm text-green-600 dark:text-green-400">
              <span>{{ successDetails }}</span>
            </p>
          </div>
        </div>
      </div>

      <!-- Bouton d'envoi -->
      <UiButton
        type="submit"
        :loading="formState.loading"
        :disabled="formState.loading || formState.success"
        size="lg"
        full-width
      >
        <template #icon v-if="!formState.loading">
          <FontAwesomeIcon icon="paper-plane" />
        </template>
        {{ formState.loading ? tUI('contact.form.sending') : tUI('contact.form.send') }}
      </UiButton>

      <p class="text-center text-xs text-gray-500 dark:text-gray-400">
        {{ tUI('contact.form.requiredFieldsNote') }}
      </p>
    </form>
  </UiCard>
</template>

<script setup lang="ts">
import { computed } from 'vue'
import { useContactStore } from '@/stores/contact'
import UiCard from '@/components/ui/UiCard.vue'
import UiButton from '@/components/ui/UiButton.vue'
import { useI18n } from '@/composables/useI18n'
import type { ContactFormData } from '@/types'

const contactStore = useContactStore()
const { formState } = contactStore
const { tUI } = useI18n()

const SUBJECT_OPTION_KEYS = ['collaboration', 'hiring', 'freelance', 'question', 'other'] as const

const subjectOptions = computed(() =>
  SUBJECT_OPTION_KEYS.map((key) => {
    const label = tUI(`contact.form.subjectOptions.${key}`)
    return {
      value: label,
      label,
    }
  })
)

const MAX_MESSAGE_LENGTH = 2000

const characterCountLabel = computed(() =>
  tUI('contact.form.characterCount')
    .replace('{count}', formState.data.message.length.toString())
    .replace('{max}', MAX_MESSAGE_LENGTH.toString())
)

const successDetails = computed(() => {
  const text = tUI('contact.form.successDetails')
  return text
})

const handleSubmit = async () => {
  await contactStore.submitForm()
}

const clearFieldError = (field: keyof ContactFormData) => {
  if (formState.errors[field as keyof typeof formState.errors]) {
    delete formState.errors[field as keyof typeof formState.errors]
  }
}
</script>
