<template>
  <span :class="badgeClasses">
    <slot />
  </span>
</template>

<script setup lang="ts">
import { computed } from 'vue'

interface Props {
  variant?: 'default' | 'primary' | 'secondary' | 'success' | 'warning' | 'danger' | 'info'
  size?: 'sm' | 'md' | 'lg'
  rounded?: boolean
  outline?: boolean
}

const props = withDefaults(defineProps<Props>(), {
  variant: 'default',
  size: 'md',
  rounded: false,
  outline: false,
})

const badgeClasses = computed(() => {
  const baseClasses = ['inline-flex items-center font-medium', 'transition-colors duration-200']

  // Tailles
  const sizeClasses = {
    sm: 'px-2 py-0.5 text-xs',
    md: 'px-2.5 py-1 text-sm',
    lg: 'px-3 py-1.5 text-base',
  }

  // Variantes
  const variantClasses = {
    default: props.outline
      ? 'bg-transparent border border-gray-300 text-gray-700 dark:border-gray-600 dark:text-gray-300'
      : 'bg-gray-100 text-gray-800 dark:bg-gray-800 dark:text-gray-200',
    primary: props.outline
      ? 'bg-transparent border border-blue-300 text-blue-700 dark:border-blue-500 dark:text-blue-400'
      : 'bg-blue-100 text-blue-800 dark:bg-blue-900/30 dark:text-blue-400',
    secondary: props.outline
      ? 'bg-transparent border border-gray-300 text-gray-700 dark:border-gray-600 dark:text-gray-300'
      : 'bg-gray-100 text-gray-800 dark:bg-gray-800 dark:text-gray-200',
    success: props.outline
      ? 'bg-transparent border border-green-300 text-green-700 dark:border-green-500 dark:text-green-400'
      : 'bg-green-100 text-green-800 dark:bg-green-900/30 dark:text-green-400',
    warning: props.outline
      ? 'bg-transparent border border-yellow-300 text-yellow-700 dark:border-yellow-500 dark:text-yellow-400'
      : 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900/30 dark:text-yellow-400',
    danger: props.outline
      ? 'bg-transparent border border-red-300 text-red-700 dark:border-red-500 dark:text-red-400'
      : 'bg-red-100 text-red-800 dark:bg-red-900/30 dark:text-red-400',
    info: props.outline
      ? 'bg-transparent border border-cyan-300 text-cyan-700 dark:border-cyan-500 dark:text-cyan-400'
      : 'bg-cyan-100 text-cyan-800 dark:bg-cyan-900/30 dark:text-cyan-400',
  }

  // Forme
  const shapeClasses = props.rounded ? 'rounded-full' : 'rounded-md'

  return [...baseClasses, sizeClasses[props.size], variantClasses[props.variant], shapeClasses]
    .filter(Boolean)
    .join(' ')
})
</script>
