<template>
  <AppSection ref="sectionRef" id="experience" class="bg-white dark:bg-gray-900">
    <!-- En-tête de section -->
    <div class="mb-16 text-center">
      <h2 class="mb-4 text-3xl font-bold text-gray-900 dark:text-white md:text-4xl">
        {{ tUI('experience.title') }}
      </h2>
      <p class="mx-auto max-w-3xl text-lg text-gray-600 dark:text-gray-300">
        {{ tUI('experience.subtitle') }}
      </p>
    </div>

    <!-- Timeline -->
    <div v-if="!loading && experiences.length > 0" class="relative">
      <!-- Ligne verticale animée -->
      <div
        ref="timelineRef"
        class="timeline-line absolute left-4 top-0 w-0.5 bg-blue-200 dark:bg-blue-800 md:left-1/2 md:-ml-0.5"
        style="height: 0px"
      ></div>

      <div class="max-w-none space-y-12">
        <div
          v-for="(experience, index) in experiences"
          :key="experience.id"
          :ref="
            (el: any) => {
              if (el) experienceRefs[index] = el
            }
          "
          class="experience-item relative"
        >
          <!-- Point sur la timeline animé - toujours centré -->
          <div
            :ref="
              (el: any) => {
                if (el) pointRefs[index] = el
              }
            "
            class="timeline-point timeline-point-centered timeline-point-hidden absolute left-4 h-6 w-6 rounded-full border-4 border-white bg-blue-600 shadow-lg dark:border-gray-900 dark:bg-blue-500 md:left-1/2"
            style="top: 50%; z-index: 20"
          ></div>

          <!-- Contenu flex avec animation flip -->
          <div
            :class="[
              'flex flex-col md:flex-row md:items-center',
              index % 2 === 0 ? 'md:flex-row' : 'md:flex-row-reverse',
            ]"
          >
            <div
              :class="[
                'experience-card experience-card-hidden ml-16 md:ml-0 md:w-1/2',
                index % 2 === 0 ? 'md:pr-12' : 'md:pl-12',
              ]"
            >
              <UiCard variant="shadow" :hover="true">
                <!-- En-tête -->
                <div class="mb-4 flex items-start justify-between">
                  <div>
                    <h3 class="text-xl font-bold text-gray-900 dark:text-white">
                      {{ experience.position }}
                    </h3>
                    <p class="text-lg font-semibold text-blue-600 dark:text-blue-400">
                      {{ experience.company }}
                    </p>
                    <div class="mt-2 flex flex-wrap gap-2">
                      <UiBadge :variant="getTypeVariant(experience.type)" size="sm">
                        {{ experience.type }}
                      </UiBadge>
                      <UiBadge variant="secondary" size="sm">
                        {{ experience.location }}
                      </UiBadge>
                    </div>
                  </div>

                  <div
                    v-if="experience.current"
                    class="flex items-center text-green-600 dark:text-green-400"
                  >
                    <div class="mr-2 h-2 w-2 animate-pulse rounded-full bg-current"></div>
                    <span class="text-sm font-medium">{{ tUI('experience.currentLabel') }}</span>
                  </div>
                </div>

                <!-- Dates -->
                <div class="mb-4 text-sm text-gray-500 dark:text-gray-400">
                  {{ formatDate(experience.startDate) }} -
                  {{ experience.current ? tUI('experience.present') : formatDate(experience.endDate!) }}
                  <span class="ml-2">({{ experience.duration }})</span>
                </div>

                <!-- Description -->
                <p class="mb-4 text-gray-600 dark:text-gray-300">
                  {{ experience.description }}
                </p>

                <!-- Réalisations -->
                <div v-if="experience.achievements.length > 0" class="mb-4">
                  <h4 class="mb-2 font-semibold text-gray-900 dark:text-white">
                    {{ `${tUI('experience.achievements')}:` }}
                  </h4>
                  <ul class="space-y-1">
                    <li
                      v-for="achievement in experience.achievements"
                      :key="achievement"
                      class="flex items-start text-sm text-gray-600 dark:text-gray-300"
                    >
                      <FontAwesomeIcon
                        icon="check-circle"
                        class="mr-2 mt-1 flex-shrink-0 text-green-500"
                      />
                      {{ achievement }}
                    </li>
                  </ul>
                </div>

                <!-- Technologies -->
                <div class="flex flex-wrap gap-1">
                  <UiBadge
                    v-for="tech in experience.technologies"
                    :key="tech"
                    variant="primary"
                    :outline="true"
                    size="sm"
                  >
                    {{ tech }}
                  </UiBadge>
                </div>
              </UiCard>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- État de chargement -->
    <div v-else-if="loading" class="py-12 text-center">
      <FontAwesomeIcon icon="spinner" class="mb-4 animate-spin text-4xl text-blue-600" />
      <p class="text-gray-600 dark:text-gray-300">{{ tUI('experience.loading') }}</p>
    </div>
  </AppSection>
</template>

<script setup lang="ts">
import { computed, ref, nextTick, watch } from 'vue'
import { useExperienceStore } from '@/stores/experience'
import { useI18n } from '@/composables/useI18n'
import AppSection from '@/components/layout/AppSection.vue'
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import UiCard from '@/components/ui/UiCard.vue'
import UiBadge from '@/components/ui/UiBadge.vue'
import type { Experience } from '@/types'

// Register GSAP plugins
if (typeof window !== 'undefined') {
  gsap.registerPlugin(ScrollTrigger)
}

const experienceStore = useExperienceStore()
const { tUI, currentLanguage } = useI18n()

const localeMap: Record<string, string> = {
  fr: 'fr-FR',
  en: 'en-US',
}

const dateFormatter = computed(() => {
  const locale = currentLanguage.value
  const resolvedLocale = localeMap[locale] ?? locale ?? 'en-US'

  return new Intl.DateTimeFormat(resolvedLocale, {
    month: 'long',
    year: 'numeric',
  })
})

// Refs pour les animations
const sectionRef = ref<HTMLElement>()
const timelineRef = ref<HTMLElement>()
const experienceRefs = ref<HTMLElement[]>([])
const pointRefs = ref<HTMLElement[]>([])
const timelineHeight = ref('0px')

const experiences = computed(() => experienceStore.experiences)
const loading = computed(() => experienceStore.loading)

type BadgeVariant = 'default' | 'primary' | 'secondary' | 'success' | 'warning' | 'danger' | 'info'

const TYPE_VARIANT_MAP: Record<string, BadgeVariant> = {
  cdi: 'success',
  'permanent contract': 'success',
  permanent: 'success',
  freelance: 'primary',
  'self-employed': 'primary',
  stage: 'warning',
  internship: 'warning',
  alternance: 'warning',
  apprenticeship: 'warning',
  cdd: 'warning',
}

const getTypeVariant = (type: Experience['type']): BadgeVariant => {
  if (!type) {
    return 'default'
  }

  const normalizedType = type.toString().trim().toLowerCase()
  return TYPE_VARIANT_MAP[normalizedType] ?? 'default'
}

const formatDate = (dateString: string) => {
  if (!dateString) {
    return ''
  }

  const date = new Date(dateString)
  if (Number.isNaN(date.getTime())) {
    return ''
  }

  return dateFormatter.value.format(date)
}

// Fonction pour calculer la hauteur totale de la timeline
const calculateTimelineHeight = () => {
  if (experienceRefs.value.length > 0) {
    const lastItem = experienceRefs.value[experienceRefs.value.length - 1]
    if (lastItem) {
      const rect = lastItem.getBoundingClientRect()
      // Accéder à l'élément DOM du composant AppSection via $el
      const containerRect = (sectionRef.value as { $el?: Element })?.$el?.getBoundingClientRect()

      if (containerRect) {
        const height = `${rect.top - containerRect.top + rect.height / 2}px`
        return height
      }
    }
  }
  return '100%'
}

// Animation de la ligne qui se dessine progressivement avec ScrollTrigger
const animateTimeline = async () => {
  if (!timelineRef.value || !sectionRef.value || experiences.value.length === 0) return

  // Obtenir l'élément DOM du composant AppSection
  const sectionElement = (sectionRef.value as { $el?: Element })?.$el
  if (!sectionElement) return

  // Calculer la hauteur finale
  const finalHeightStr = calculateTimelineHeight()
  const finalHeight = parseInt(finalHeightStr.replace('px', ''))

  // Initialiser la ligne à hauteur 0
  gsap.set(timelineRef.value, { height: 0 })

  // Animation progressive de la ligne avec ScrollTrigger global
  let currentHeight = 0
  const updateTimeline = (progress: number) => {
    if (!timelineRef.value) return

    const targetHeight = finalHeight * progress
    // S'assurer que la ligne ne diminue jamais
    if (targetHeight > currentHeight) {
      currentHeight = targetHeight
      gsap.set(timelineRef.value, { height: `${currentHeight}px` })
    }
  }

  ScrollTrigger.create({
    trigger: sectionElement,
    start: 'top 95%',
    end: 'bottom 5%',
    onUpdate: (self) => updateTimeline((self as { progress: number }).progress),
    onEnter: () => updateTimeline(0),
    onLeave: () => updateTimeline(1),
  })

  // Créer un ScrollTrigger individuel pour chaque élément
  experienceRefs.value.forEach((experienceElement, index) => {
    if (!experienceElement) return

    const point = pointRefs.value[index]
    if (!point) return

    ScrollTrigger.create({
      trigger: experienceElement,
      start: 'top 80%',
      end: 'bottom 20%',
      onEnter: () => {
        if (!point.classList.contains('animated')) {
          animateExperienceItem(index)
        }
      },
    })
  })
}

// Animation d'un élément spécifique (point + carte)
const animateExperienceItem = (index: number) => {
  const point = pointRefs.value[index]
  if (!point) return

  // Marquer comme animé pour éviter les répétitions
  point.classList.add('animated')

  // Retirer la classe qui masque le point
  point.classList.remove('timeline-point-hidden')

  // Animer l'apparition du point
  gsap.fromTo(
    point,
    {
      scale: 0,
      opacity: 0,
    },
    {
      scale: 1,
      opacity: 1,
      duration: 0.5,
      ease: 'back.out(1.7)',
      onComplete: () => {
        // Ajouter l'effet de pulse continu
        gsap.to(point, {
          scale: 1.15,
          duration: 1,
          repeat: -1,
          yoyo: true,
          ease: 'power2.inOut',
        })

        // Ajouter un effet de glow
        gsap.to(point, {
          boxShadow: '0 0 20px rgba(59, 130, 246, 0.6)',
          duration: 1,
          repeat: -1,
          yoyo: true,
          ease: 'power2.inOut',
        })
      },
    }
  )

  // Animer la carte correspondante
  animateExperienceCard(index)
}

// Animation Flip Card pour une carte spécifique
const animateExperienceCard = (index: number) => {
  const card = experienceRefs.value[index]
  if (card) {
    const cardElement = card.querySelector('.experience-card')
    if (cardElement) {
      // Retirer la classe qui masque la carte
      cardElement.classList.remove('experience-card-hidden')

      // Animer le flip de la carte
      gsap.fromTo(
        cardElement,
        {
          rotationY: -90,
          opacity: 0,
          transformPerspective: 1000,
          transformOrigin: 'center center',
        },
        {
          rotationY: 0,
          opacity: 1,
          duration: 0.8,
          delay: 0.2, // Petit délai après l'apparition du point
          ease: 'power2.out',
        }
      )
    }
  }
}

// Animation des points qui pulsent (version simplifiée pour l'initialisation)
const initializePoints = () => {
  pointRefs.value.forEach((point) => {
    if (point) {
      // Les points sont déjà masqués par la classe CSS timeline-point-hidden
      // Supprimer la classe animated si elle existe
      point.classList.remove('animated')
    }
  })
}

// Initialisation des cartes
const initializeCards = () => {
  experienceRefs.value.forEach((card) => {
    if (card) {
      const cardElement = card.querySelector('.experience-card')
      if (cardElement) {
        // Les cartes sont déjà masquées par la classe CSS experience-card-hidden
        // Pas besoin de GSAP.set
      }
    }
  })
}

// Fonction principale d'animation
const startTimelineAnimation = async () => {
  if (experiences.value.length === 0) return

  // Attendre que les éléments soient montés
  await nextTick()

  // IMPORTANT: Initialiser immédiatement les éléments invisibles
  initializePoints()
  initializeCards()

  // Calculer et définir la hauteur finale de la timeline
  timelineHeight.value = calculateTimelineHeight()

  // Démarrer l'animation de la timeline (qui gère maintenant le scroll)
  animateTimeline()
}

// Démarrer les animations quand les données sont disponibles
watch(
  () => experienceStore.experiences,
  async () => {
    if (experienceStore.experiences.length > 0) {
      await nextTick()
      startTimelineAnimation()
    }
  },
  { immediate: true }
)
</script>

<style scoped>
/* Animations pour la timeline */
.timeline-line {
  transition: height 0.3s ease-out;
  transform-origin: top;
}

.timeline-point {
  transition: all 0.3s ease-out;
  transform-style: preserve-3d;
}

.timeline-point-centered {
  transform: translateY(-50%);
}

/* Masquer initialement les points et cartes */
.timeline-point-hidden {
  opacity: 0;
  transform: translateY(-50%) scale(0);
}

.experience-card-hidden {
  opacity: 0;
  transform: rotateY(-90deg);
  transform-style: preserve-3d;
  transform-origin: center center;
  perspective: 1000px;
}

@media (min-width: 768px) {
  .timeline-point-centered {
    transform: translate(-50%, -50%);
  }

  .timeline-point-hidden {
    transform: translate(-50%, -50%) scale(0);
  }

  .timeline-point-centered:hover {
    transform: translate(-50%, -50%) scale(1.1);
  }
}

.timeline-point:hover {
  transform: translateY(-50%) scale(1.1);
}

/* Animation flip card pour les cartes d'expérience */
.experience-card {
  transform-style: preserve-3d;
  backface-visibility: hidden;
  transition: transform 0.3s ease-out;
}

/* Animation au survol des cartes */
.experience-item:hover .experience-card {
  transform: translateY(-5px);
}

/* Effet de glow pour les points actifs */
.timeline-point {
  position: relative;
}

.timeline-point::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(59, 130, 246, 0.3) 0%, transparent 70%);
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: opacity 0.3s ease-out;
  pointer-events: none;
}

.timeline-point:hover::after {
  opacity: 1;
}

/* Amélioration des animations avec prefers-reduced-motion */
@media (prefers-reduced-motion: reduce) {
  .timeline-line,
  .timeline-point,
  .experience-card {
    transition: none !important;
    animation: none !important;
  }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .experience-card {
    transform: none !important;
  }

  .experience-item:hover .experience-card {
    transform: none;
  }
}
</style>
