<template>
  <div
    ref="cardRef"
    class="project-card group relative cursor-pointer overflow-hidden rounded-2xl"
    :class="cardClasses"
    @click="handleClick"
  >
    <!-- Image de fond avec effet parallax -->
    <div class="absolute inset-0 overflow-hidden">
      <div
        ref="imageRef"
        class="h-full w-full bg-cover bg-center transition-transform duration-700"
        :style="{ backgroundImage: `url(${baseImageUrl})` }"
      ></div>

      <!-- Overlay dynamique -->
      <div
        ref="overlayRef"
        class="absolute inset-0 bg-black/50 transition-all duration-500 group-hover:bg-black/30"
      ></div>
    </div>

    <!-- Badge de statut -->
    <div class="absolute right-4 top-4 z-20">
      <span
        class="rounded-full border border-white/30 bg-white/20 px-3 py-1 text-xs font-medium backdrop-blur-sm"
        :class="getStatusClass(project.status)"
      >
        {{ project.status }}
      </span>
    </div>

    <!-- Technologies utilisées (coins supérieurs) -->
    <div class="absolute left-4 top-4 z-20 flex space-x-2">
      <div
        v-for="tech in project.technologies.slice(0, 3)"
        :key="tech"
        :class="[
          'flex h-8 w-8 items-center justify-center rounded-full border-2 border-white/50 text-white shadow-lg backdrop-blur-sm',
          getTechColor(tech),
        ]"
        :title="tech"
      >
        <FontAwesomeIcon
          v-if="getTechIcon(tech).startsWith('fab fa-')"
          :icon="['fab', getTechIcon(tech).replace('fab fa-', '')]"
          class="text-sm drop-shadow-sm"
        />
        <FontAwesomeIcon
          v-else-if="getTechIcon(tech).startsWith('fas fa-')"
          :icon="getTechIcon(tech).replace('fas fa-', '')"
          class="text-sm drop-shadow-sm"
        />
        <i v-else :class="getTechIcon(tech)" class="text-sm drop-shadow-sm"></i>
      </div>
      <div
        v-if="project.technologies.length > 3"
        class="flex h-8 w-8 items-center justify-center rounded-full border-2 border-white/50 bg-gray-600 text-white shadow-lg backdrop-blur-sm"
        :title="`+${project.technologies.length - 3} autres`"
      >
        <span class="text-xs font-bold drop-shadow-sm">+{{ project.technologies.length - 3 }}</span>
      </div>
    </div>

    <!-- Contenu principal -->
    <div class="absolute bottom-0 left-0 right-0 z-10 p-6 text-white">
      <!-- Titre avec animation -->
      <h3
        ref="titleRef"
        class="mb-2 transform text-xl font-bold transition-all duration-300 group-hover:translate-y-[-4px]"
      >
        {{ project.title }}
      </h3>

      <!-- Description -->
      <p
        ref="descriptionRef"
        class="mb-4 line-clamp-2 transform text-sm text-white/90 opacity-80 transition-all duration-300 group-hover:opacity-100"
      >
        {{ project.description }}
      </p>

      <!-- Métriques du projet -->
      <div
        ref="metricsRef"
        class="mb-4 flex translate-y-4 transform items-center space-x-4 text-xs text-white/80 opacity-0 transition-all duration-500 group-hover:translate-y-0 group-hover:opacity-100"
      >
        <div v-if="project.stars" class="flex items-center space-x-1">
          <FontAwesomeIcon icon="star" class="text-yellow-400" />
          <span>{{ project.stars }}</span>
        </div>
        <div v-if="project.forks" class="flex items-center space-x-1">
          <FontAwesomeIcon icon="code-branch" class="text-blue-400" />
          <span>{{ project.forks }}</span>
        </div>
        <div v-if="project.year" class="flex items-center space-x-1">
          <FontAwesomeIcon icon="calendar" class="text-green-400" />
          <span>{{ project.year }}</span>
        </div>
      </div>

      <!-- Actions -->
      <div
        ref="actionsRef"
        class="flex translate-y-6 transform space-x-3 opacity-0 transition-all duration-500 group-hover:translate-y-0 group-hover:opacity-100"
        style="transition-delay: 100ms"
      >
        <UiButton
          v-if="project.demoUrl"
          variant="primary"
          size="sm"
          class="border-white/30 bg-white/20 backdrop-blur-sm hover:bg-white/30"
          @click.stop="openDemo"
        >
          <FontAwesomeIcon icon="external-link-alt" class="mr-1" />
          Demo
        </UiButton>

        <UiButton
          v-if="project.figmaUrl"
          variant="outline"
          size="sm"
          class="border-white/30 bg-white/10 backdrop-blur-sm hover:bg-white/20"
          @click.stop="openFigma"
        >
          <FontAwesomeIcon :icon="['fab', 'figma']" class="mr-1" />
          Figma
        </UiButton>

        <UiButton
          v-if="project.githubUrl"
          variant="outline"
          size="sm"
          class="border-white/30 bg-white/10 backdrop-blur-sm hover:bg-white/20"
          @click.stop="openGithub"
        >
          <FontAwesomeIcon :icon="['fab', 'github']" class="mr-1" />
          Code
        </UiButton>

        <UiButton
          variant="outline"
          size="sm"
          class="border-white/30 bg-white/10 backdrop-blur-sm hover:bg-white/20"
          @click.stop="viewDetails"
        >
          <FontAwesomeIcon icon="info-circle" class="mr-1" />
          Détails
        </UiButton>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
// Compute the correct image URL for dev/prod
const baseImageUrl = computed(() => {
  if (!props.project.image) return ''
  // Remove leading slash if present
  const img = props.project.image.startsWith('/')
    ? props.project.image.slice(1)
    : props.project.image
  return import.meta.env.BASE_URL + img
})
import { ref, computed } from 'vue'
import UiButton from './UiButton.vue'

interface Project {
  id: string
  title: string
  description: string
  image: string
  technologies: string[]
  status: string // Accepte n'importe quelle chaîne
  demoUrl?: string
  githubUrl?: string
  figmaUrl?: string
  year?: number | string // Accepte les deux formats
  difficulty?: string // Plus flexible
  stars?: number
  forks?: number
  links?: {
    demo?: string | null
    github?: string | null
    case_study?: string | null
    figma?: string | null
  }
}

interface Props {
  project: Project
  variant?: 'default' | 'featured' | 'compact'
}

const props = withDefaults(defineProps<Props>(), {
  variant: 'default',
})

const emit = defineEmits<{
  click: [project: Project]
  demo: [project: Project]
  github: [project: Project]
  details: [project: Project]
  figma: [project: Project]
}>()

// Refs
const cardRef = ref<HTMLElement>()
const imageRef = ref<HTMLElement>()
const overlayRef = ref<HTMLElement>()
const titleRef = ref<HTMLElement>()
const descriptionRef = ref<HTMLElement>()
const metricsRef = ref<HTMLElement>()
const actionsRef = ref<HTMLElement>()

// Classes calculées
const cardClasses = computed(() => [
  'h-80',
  {
    'h-96': props.variant === 'featured',
    'h-64': props.variant === 'compact',
  },
])

// Fonctions utilitaires
const getTechIcon = (tech: string) => {
  const icons: Record<string, string> = {
    'Vue.js': 'fab fa-vuejs',
    React: 'fab fa-react',
    Angular: 'fab fa-angular',
    'Node.js': 'fab fa-node-js',
    Laravel: 'fab fa-laravel',
    PHP: 'fab fa-php',
    Python: 'fab fa-python',
    JavaScript: 'fab fa-js',
    TypeScript: 'fas fa-code',
    HTML: 'fab fa-html5',
    CSS: 'fab fa-css3-alt',
    TailwindCSS: 'fas fa-wind',
    SASS: 'fab fa-sass',
    Docker: 'fab fa-docker',
    Git: 'fab fa-git-alt',
    AWS: 'fab fa-aws',
    Firebase: 'fas fa-fire',
    Dart: 'fas fa-code',
    Flutter: 'fas fa-mobile-alt',
    C: 'fas fa-code',
    PyQt6: 'fas fa-window-maximize',
    Astroquery: 'fas fa-satellite',
    Astropy: 'fas fa-star',
    SQLite: 'fas fa-database',
    PostgreSQL: 'fas fa-database',
    'API Open Trivia': 'fas fa-question-circle',
    Tkinter: 'fas fa-window-restore',
    'API Open-Meteo': 'fas fa-cloud-sun',
  }
  return icons[tech] || 'fas fa-cube'
}

const getTechColor = (tech: string) => {
  const colors: Record<string, string> = {
    'Vue.js': 'bg-green-500',
    React: 'bg-blue-500',
    Angular: 'bg-red-500',
    'Node.js': 'bg-green-600',
    Laravel: 'bg-red-600',
    PHP: 'bg-purple-600',
    Python: 'bg-yellow-500',
    JavaScript: 'bg-yellow-400',
    TypeScript: 'bg-blue-600',
    HTML: 'bg-orange-500',
    CSS: 'bg-blue-400',
    SASS: 'bg-pink-500',
    Docker: 'bg-blue-700',
    Git: 'bg-orange-600',
    AWS: 'bg-orange-700',
  }
  return colors[tech] || 'bg-gray-500'
}

const getStatusClass = (status: string) => {
  switch (status.toLowerCase()) {
    case 'terminé':
    case 'completed':
      return 'bg-green-500/20 text-green-300'
    case 'en développement':
    case 'in development':
      return 'bg-yellow-500/20 text-yellow-300'
    case 'en cours':
    case 'in progress':
      return 'bg-blue-500/20 text-blue-300'
    default:
      return 'bg-gray-500/20 text-gray-300'
  }
}

// Gestion des événements
const handleClick = () => {
  emit('click', props.project)
}

const openDemo = () => {
  if (props.project.demoUrl) {
    window.open(props.project.demoUrl, '_blank')
    emit('demo', props.project)
  }
}

const openGithub = () => {
  if (props.project.githubUrl) {
    window.open(props.project.githubUrl, '_blank')
    emit('github', props.project)
  }
}

const openFigma = () => {
  if (props.project.figmaUrl) {
    window.open(props.project.figmaUrl, '_blank')
    emit('figma', props.project)
  }
}

const viewDetails = () => {
  emit('details', props.project)
}
</script>

<style scoped>
.project-card {
  /* Ajout d'une ombre portée par défaut */
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
}

.project-card:hover {
  /* Ombre plus prononcée au survol */
  box-shadow: 0 8px 60px rgba(0, 0, 0, 0.2);
}
</style>
