import { defineStore } from 'pinia'
import { ref, computed } from 'vue'
import type { Project } from '@/types'
import { useLocalizedData } from '@/composables/useLocalizedData'

export const useProjectsStore = defineStore('projects', () => {
  const { createLocalizedRef } = useLocalizedData()

  // Utiliser createLocalizedRef pour avoir des données réactives
  const projects = createLocalizedRef<Project[]>('projects', [])
  const loading = ref(false)
  const error = ref<string | null>(null)

  // Computed
  const featuredProjects = computed(() => projects.value.filter((project) => project.featured))

  const projectsByCategory = computed(() => {
    const categories: Record<string, Project[]> = {}
    projects.value.forEach((project) => {
      if (!categories[project.category]) {
        categories[project.category] = []
      }
      categories[project.category]?.push(project)
    })
    return categories
  })

  const categories = computed(() => [...new Set(projects.value.map((p) => p.category))])

  // Actions
  const getProjectBySlug = (slug: string) => {
    return projects.value.find((project) => project.slug === slug)
  }

  const getProjectsByTechnology = (tech: string) => {
    return projects.value.filter((project) =>
      project.technologies.some((t) => t.toLowerCase().includes(tech.toLowerCase()))
    )
  }

  const searchProjects = (query: string) => {
    const searchTerm = query.toLowerCase()
    return projects.value.filter(
      (project) =>
        project.title.toLowerCase().includes(searchTerm) ||
        project.description.toLowerCase().includes(searchTerm) ||
        project.technologies.some((tech) => tech.toLowerCase().includes(searchTerm)) ||
        project.category.toLowerCase().includes(searchTerm)
    )
  }

  return {
    // State
    projects,
    loading,
    error,

    // Getters
    featuredProjects,
    projectsByCategory,
    categories,

    // Actions
    getProjectBySlug,
    getProjectsByTechnology,
    searchProjects,
  }
})
