<template>
  <div class="relative">
    <!-- Navigation arrows -->
    <button
      v-if="showNavigation"
      @click="previous"
      :disabled="isAtStart"
      class="absolute left-0 top-1/2 z-10 -translate-y-1/2 rounded-full bg-white p-2 shadow-md transition-all duration-200 hover:shadow-lg disabled:cursor-not-allowed disabled:opacity-50 dark:bg-gray-800"
      aria-label="Témoignage précédent"
    >
      <FontAwesomeIcon icon="chevron-left" class="text-gray-600 dark:text-gray-300" />
    </button>

    <button
      v-if="showNavigation"
      @click="next"
      :disabled="isAtEnd"
      class="absolute right-0 top-1/2 z-10 -translate-y-1/2 rounded-full bg-white p-2 shadow-md transition-all duration-200 hover:shadow-lg disabled:cursor-not-allowed disabled:opacity-50 dark:bg-gray-800"
      aria-label="Témoignage suivant"
    >
      <FontAwesomeIcon icon="chevron-right" class="text-gray-600 dark:text-gray-300" />
    </button>

    <!-- Carousel container -->
    <div
      ref="carouselRef"
      class="mx-8 overflow-hidden"
      @mouseenter="pauseAutoplay"
      @mouseleave="resumeAutoplay"
    >
      <div
        class="flex transition-transform duration-500 ease-in-out"
        :style="{ transform: `translateX(-${currentIndex * slideWidth}%)` }"
      >
        <div
          v-for="testimonial in testimonials"
          :key="testimonial.id"
          :class="['flex-shrink-0 px-4', slideWidthClass]"
        >
          <TestimonialCard :testimonial="testimonial" />
        </div>
      </div>
    </div>

    <!-- Progress indicators -->
    <div v-if="showIndicators" class="mt-6 flex justify-center space-x-2">
      <button
        v-for="(_, index) in totalSlides"
        :key="index"
        @click="goToSlide(index)"
        :class="[
          'h-2 w-2 rounded-full transition-all duration-300',
          index === currentIndex
            ? 'w-8 bg-blue-600'
            : 'bg-gray-300 hover:bg-gray-400 dark:bg-gray-600',
        ]"
        :aria-label="`Aller au témoignage ${index + 1}`"
      />
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted, onUnmounted, watch } from 'vue'
import type { Testimonial } from '@/stores/testimonials'
import TestimonialCard from './TestimonialCard.vue'

export interface TestimonialsCarouselProps {
  testimonials: Testimonial[]
  slidesToShow?: number
  autoplay?: boolean
  autoplayDelay?: number
  showNavigation?: boolean
  showIndicators?: boolean
  loop?: boolean
}

const props = withDefaults(defineProps<TestimonialsCarouselProps>(), {
  slidesToShow: 1,
  autoplay: true,
  autoplayDelay: 5000,
  showNavigation: true,
  showIndicators: true,
  loop: true,
})

// State
const currentIndex = ref(0)
const carouselRef = ref<HTMLDivElement>()
const autoplayTimer = ref<ReturnType<typeof setInterval> | undefined>()
const isPaused = ref(false)

const testimonials = computed(() => props.testimonials)

// Computed
const totalSlides = computed(() => Math.ceil(props.testimonials.length / props.slidesToShow))

const slideWidth = computed(() => 100 / props.slidesToShow)

const slideWidthClass = computed(() => {
  const widths = {
    1: 'w-full',
    2: 'w-1/2',
    3: 'w-1/3',
    4: 'w-1/4',
  }
  return widths[props.slidesToShow as keyof typeof widths] || 'w-full'
})

const isAtStart = computed(() => currentIndex.value === 0)
const isAtEnd = computed(() => currentIndex.value >= totalSlides.value - 1)

// Methods
const next = () => {
  if (isAtEnd.value) {
    if (props.loop) {
      currentIndex.value = 0
    }
  } else {
    currentIndex.value++
  }
}

const previous = () => {
  if (isAtStart.value) {
    if (props.loop) {
      currentIndex.value = totalSlides.value - 1
    }
  } else {
    currentIndex.value--
  }
}

const goToSlide = (index: number) => {
  currentIndex.value = index
}

const startAutoplay = () => {
  if (props.autoplay && !isPaused.value) {
    autoplayTimer.value = setInterval(next, props.autoplayDelay)
  }
}

const stopAutoplay = () => {
  if (autoplayTimer.value) {
    clearInterval(autoplayTimer.value)
    autoplayTimer.value = undefined
  }
}

const pauseAutoplay = () => {
  isPaused.value = true
  stopAutoplay()
}

const resumeAutoplay = () => {
  isPaused.value = false
  startAutoplay()
}

watch(
  () => [props.testimonials, props.slidesToShow],
  () => {
    // Reset index if content or layout changes to avoid empty slots
    if (currentIndex.value >= totalSlides.value) {
      currentIndex.value = 0
    }
  },
  { deep: true }
)

// Lifecycle
onMounted(() => {
  startAutoplay()
})

onUnmounted(() => {
  stopAutoplay()
})
</script>
