<template>
  <AppSection
    ref="sectionRef"
    id="skills"
    class="relative overflow-hidden bg-gray-50 dark:bg-gray-800"
  >
    <!-- Background animé -->
    <div class="absolute inset-0 opacity-30">
      <div
        class="animate-float-slow absolute left-1/4 top-1/4 h-96 w-96 rounded-full bg-blue-500/10 blur-3xl"
      ></div>
      <div
        class="animate-float-reverse absolute bottom-1/4 right-1/4 h-96 w-96 rounded-full bg-purple-500/10 blur-3xl"
      ></div>
    </div>

    <div class="relative">
      <!-- En-tête de section -->
      <div class="mb-16 text-center">
        <h2
          class="mb-4 bg-gradient-to-r from-blue-600 via-purple-600 to-pink-600 bg-clip-text text-3xl font-bold text-gray-900 text-transparent dark:text-white md:text-4xl"
        >
          {{ tUI('skills.title') }}
        </h2>
        <p class="mx-auto max-w-3xl text-lg text-gray-600 dark:text-gray-300">
          {{ tUI('skills.subtitle') }}
        </p>
        <!-- Ligne de séparation animée -->
        <div
          class="mx-auto mt-6 h-1 w-24 rounded-full bg-gradient-to-r from-blue-500 to-purple-500"
        ></div>
      </div>

      <!-- Compétences par catégorie -->
      <div v-if="!loading && skills.length > 0" class="space-y-16">
        <div v-for="category in skills" :key="category.category" class="text-center">
          <!-- Titre de catégorie avec icône -->
          <div class="mb-12 flex items-center justify-center">
            <div
              class="h-px flex-1 bg-gradient-to-r from-transparent via-gray-300 to-transparent dark:via-gray-600"
            ></div>
            <h3
              class="mx-6 rounded-full border border-gray-200 bg-white/50 px-4 py-2 text-2xl font-bold text-gray-900 backdrop-blur-sm dark:border-gray-700 dark:bg-gray-900/50 dark:text-white"
            >
              <FontAwesomeIcon
                :icon="getCategoryIcon(category.category)"
                class="mr-3 text-blue-500"
              />
              {{ category.category }}
            </h3>
            <div
              class="h-px flex-1 bg-gradient-to-r from-transparent via-gray-300 to-transparent dark:via-gray-600"
            ></div>
          </div>

          <!-- Grille de compétences holographiques -->
          <div class="grid grid-cols-1 gap-6 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4">
            <UiHolographicCard
              v-for="(skill, skillIndex) in category.skills"
              :key="skill.name"
              :title="skill.name"
              :description="formatSkillExperience(skill.years)"
              :icon="skill.icon"
              :color="skill.color"
              :level="skill.level"
              :variant="getSkillVariant(skill.level)"
              size="md"
              :particles="true"
              :style="{ animationDelay: `${skillIndex * 0.1}s` }"
              class="skill-card"
              @click="handleSkillClick(skill)"
              @hover="handleSkillHover"
            >
              <!-- Badge de progression -->
              <template #default>
                <div class="mt-2">
                  <div class="mb-2 h-2 w-full rounded-full bg-gray-200 dark:bg-gray-700">
                    <div
                      class="h-2 rounded-full transition-all duration-1000 ease-out"
                      :class="getProgressBarClass(skill.level)"
                      :style="{ width: getProgressWidth(skill.level) }"
                    ></div>
                  </div>
                  <span class="text-xs text-gray-500 dark:text-gray-400">
                    {{ getProgressPercentage(skill.level) }}%
                  </span>
                </div>
              </template>
            </UiHolographicCard>
          </div>
        </div>
      </div>

      <!-- État de chargement avec animation -->
      <div v-else-if="loading" class="py-12 text-center">
        <div class="relative">
          <div
            class="absolute inset-0 animate-pulse rounded-full bg-gradient-to-r from-blue-500 to-purple-500 opacity-20 blur"
          ></div>
          <FontAwesomeIcon
            icon="code"
            class="relative mb-4 animate-bounce text-6xl text-blue-600"
          />
        </div>
        <p class="text-lg text-gray-600 dark:text-gray-300">{{ tUI('skills.loading') }}</p>
        <div class="mt-4 flex justify-center space-x-1">
          <div class="h-2 w-2 animate-bounce rounded-full bg-blue-500"></div>
          <div
            class="h-2 w-2 animate-bounce rounded-full bg-purple-500"
            style="animation-delay: 0.1s"
          ></div>
          <div
            class="h-2 w-2 animate-bounce rounded-full bg-pink-500"
            style="animation-delay: 0.2s"
          ></div>
        </div>
      </div>

      <!-- Statistiques rapides -->
      <div v-if="!loading && skills.length > 0" class="mt-20 text-center">
        <div class="grid grid-cols-2 gap-8 md:grid-cols-4">
          <div class="stat-item">
            <div class="mb-2 text-3xl font-bold text-blue-600">{{ totalSkills }}</div>
            <div class="text-gray-600 dark:text-gray-400">{{ tUI('skills.technologies') }}</div>
          </div>
          <div class="stat-item">
            <div class="mb-2 text-3xl font-bold text-purple-600">{{ expertSkills }}</div>
            <div class="text-gray-600 dark:text-gray-400">{{ tUI('skills.expert') }}</div>
          </div>
          <div class="stat-item">
            <div class="mb-2 text-3xl font-bold text-green-600">{{ advancedSkills }}</div>
            <div class="text-gray-600 dark:text-gray-400">{{ tUI('skills.advanced') }}</div>
          </div>
          <div class="stat-item">
            <div class="mb-2 text-3xl font-bold text-yellow-600">{{ averageExperience }}</div>
            <div class="text-gray-600 dark:text-gray-400">
              {{ tUI('skills.yearsOfExperience') }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </AppSection>
</template>

<script setup lang="ts">
import { computed } from 'vue'
import { useSkillsStore } from '@/stores/skills'
import { useSectionReveal } from '@/composables/useSectionReveal'
import { useI18n } from '@/composables/useI18n'
import AppSection from '@/components/layout/AppSection.vue'
import UiHolographicCard from '@/components/ui/UiHolographicCard.vue'
import type { Skill } from '@/types'

const skillsStore = useSkillsStore()
const { tUI } = useI18n()

// Animation d'apparition de la section
const { sectionRef } = useSectionReveal({
  animation: 'fadeIn',
  duration: 800,
  delay: 100,
})

const skills = computed(() => skillsStore.skills)
const loading = computed(() => skillsStore.loading)

// Statistiques calculées
const totalSkills = computed(() => {
  return skills.value.reduce((total, category) => total + category.skills.length, 0)
})

const expertSkills = computed(() => {
  return skills.value.reduce((total, category) => {
    return total + category.skills.filter((skill) => skill.level === 'Expert').length
  }, 0)
})

const advancedSkills = computed(() => {
  return skills.value.reduce((total, category) => {
    return total + category.skills.filter((skill) => skill.level === 'Avancé').length
  }, 0)
})

const averageExperience = computed(() => {
  const allSkills = skills.value.flatMap((category) => category.skills)
  const totalYears = allSkills.reduce((total, skill) => total + skill.years, 0)
  return allSkills.length > 0 ? Math.round(totalYears / allSkills.length) : 0
})

// Fonctions utilitaires
const getCategoryIcon = (category: string) => {
  const icons: Record<string, string> = {
    Frontend: 'laptop-code',
    Backend: 'server',
    Database: 'database',
    DevOps: 'cloud',
    Mobile: 'mobile-alt',
    Design: 'palette',
    Tools: 'tools',
    Languages: 'code',
  }
  return icons[category] || 'cog'
}

const getSkillVariant = (level: string) => {
  switch (level.toLowerCase()) {
    case 'expert':
      return 'success'
    case 'avancé':
    case 'advanced':
      return 'primary'
    case 'intermédiaire':
    case 'intermediate':
      return 'warning'
    case 'débutant':
    case 'beginner':
      return 'secondary'
    default:
      return 'primary'
  }
}

const getProgressWidth = (level: string) => {
  switch (level.toLowerCase()) {
    case 'expert':
      return '100%'
    case 'avancé':
    case 'advanced':
      return '80%'
    case 'intermédiaire':
    case 'intermediate':
      return '60%'
    case 'débutant':
    case 'beginner':
      return '40%'
    default:
      return '50%'
  }
}

const getProgressPercentage = (level: string) => {
  switch (level.toLowerCase()) {
    case 'expert':
      return 100
    case 'avancé':
    case 'advanced':
      return 80
    case 'intermédiaire':
    case 'intermediate':
      return 60
    case 'débutant':
    case 'beginner':
      return 40
    default:
      return 50
  }
}

const getProgressBarClass = (level: string) => {
  switch (level.toLowerCase()) {
    case 'expert':
      return 'bg-gradient-to-r from-green-500 to-emerald-500'
    case 'avancé':
    case 'advanced':
      return 'bg-gradient-to-r from-blue-500 to-cyan-500'
    case 'intermédiaire':
    case 'intermediate':
      return 'bg-gradient-to-r from-yellow-500 to-orange-500'
    case 'débutant':
    case 'beginner':
      return 'bg-gradient-to-r from-gray-500 to-slate-500'
    default:
      return 'bg-gradient-to-r from-blue-500 to-purple-500'
  }
}

const formatSkillExperience = (years: number) => {
  const key = years === 1 ? 'skills.cardExperienceSingular' : 'skills.cardExperiencePlural'
  const template = tUI(key)

  return template.replace('{count}', years.toString())
}

// Gestionnaires d'événements
const handleSkillClick = (_skill: Skill) => {
  // Ici on pourrait ouvrir un modal avec plus de détails
}

const handleSkillHover = (_isHovered: boolean) => {
  // Animation au survol si nécessaire
}
</script>

<style scoped>
.skill-card {
  animation: slideInUp 0.6s ease-out forwards;
  opacity: 0;
  transform: translateY(30px);
}

.stat-item {
  padding: 1.5rem;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 0.75rem;
  backdrop-filter: blur(8px);
  border: 1px solid rgba(229, 231, 235, 1);
  transition: all 0.3s ease;
}

.dark .stat-item {
  background: rgba(17, 24, 39, 0.5);
  border-color: rgba(75, 85, 99, 1);
}

.stat-item:hover {
  background: rgba(255, 255, 255, 0.7);
  transform: scale(1.05);
}

.dark .stat-item:hover {
  background: rgba(17, 24, 39, 0.7);
}

@keyframes slideInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes float-slow {
  0%,
  100% {
    transform: translateY(0px) rotate(0deg);
  }
  50% {
    transform: translateY(-20px) rotate(180deg);
  }
}

@keyframes float-reverse {
  0%,
  100% {
    transform: translateY(0px) rotate(0deg);
  }
  50% {
    transform: translateY(20px) rotate(-180deg);
  }
}

.animate-float-slow {
  animation: float-slow 6s ease-in-out infinite;
}

.animate-float-reverse {
  animation: float-reverse 8s ease-in-out infinite;
}
</style>
