<template>
  <UiCard class="h-full">
    <div class="flex h-full flex-col">
      <!-- Header avec avatar et infos -->
      <div class="mb-4 flex items-start space-x-4">
        <template v-if="testimonial.avatar === 'guest'">
          <div
            class="flex h-10 w-10 items-center justify-center rounded-full bg-gray-100 dark:bg-gray-800"
          >
            <FontAwesomeIcon icon="user" class="text-2xl text-gray-400 dark:text-gray-500" />
          </div>
        </template>
        <template v-else>
          <Avatar
            :src="testimonial.avatar"
            :alt="`${testimonial.name} avatar`"
            :name="testimonial.name"
            size="lg"
          />
        </template>
        <div class="min-w-0 flex-1">
          <h3 class="truncate font-semibold text-gray-900 dark:text-white">
            {{ testimonial.name }}
          </h3>
          <p class="truncate text-sm text-gray-600 dark:text-gray-400">
            {{ testimonial.role }}
          </p>
          <div class="mt-1 flex items-center">
            <div
              v-if="testimonial.companyLogo"
              ref="companyLogoContainer"
              class="mr-1 flex h-4 w-4 items-center justify-center overflow-hidden rounded-full"
              :style="{ willChange: 'transform, opacity' }"
            >
              <img
                v-if="isCompanyLogoVisible"
                :src="companyLogoUrl"
                :alt="`${testimonial.company} logo`"
                class="h-full w-full object-contain"
                loading="lazy"
                decoding="async"
              />
              <div
                v-else
                class="h-full w-full animate-pulse rounded-full bg-gray-200 dark:bg-gray-700"
              ></div>
            </div>
            <p class="truncate text-sm text-gray-500 dark:text-gray-500">
              {{ testimonial.company }}
            </p>
          </div>
        </div>
        <div class="flex items-center space-x-2">
          <Rating :model-value="testimonial.rating" readonly size="sm" />
          <span class="text-sm text-gray-500 dark:text-gray-400"> {{ testimonial.rating }}/5 </span>
        </div>
      </div>

      <!-- Contenu du témoignage -->
      <div class="flex-1">
        <blockquote class="italic leading-relaxed text-gray-700 dark:text-gray-300">
          "{{ testimonial.content }}"
        </blockquote>
      </div>

      <!-- Footer avec date et tags -->
      <div class="mt-4 border-t border-gray-200 pt-4 dark:border-gray-700">
        <div class="mb-2 flex items-center justify-between">
          <time class="text-xs text-gray-500 dark:text-gray-400">
            {{ formatDate(testimonial.date) }}
          </time>
          <UiBadge v-if="testimonial.featured" variant="primary" size="sm">
            <FontAwesomeIcon icon="star" class="mr-1" />
            Mis en avant
          </UiBadge>
        </div>

        <!-- Tags -->
        <div v-if="testimonial.tags.length > 0" class="flex flex-wrap gap-1">
          <UiBadge
            v-for="tag in testimonial.tags.slice(0, 3)"
            :key="tag"
            variant="secondary"
            size="sm"
          >
            {{ tag }}
          </UiBadge>
          <UiBadge v-if="testimonial.tags.length > 3" variant="secondary" size="sm">
            +{{ testimonial.tags.length - 3 }}
          </UiBadge>
        </div>
      </div>
    </div>
  </UiCard>
</template>

<script setup lang="ts">
import { computed, nextTick, onMounted, onUnmounted, ref } from 'vue'
import type { Testimonial } from '@/stores/testimonials'
import UiCard from '@/components/ui/UiCard.vue'
import UiBadge from '@/components/ui/UiBadge.vue'
import Avatar from '@/components/common/Avatar.vue'
import Rating from '@/components/common/Rating.vue'

const props = defineProps<{
  testimonial: Testimonial
}>()

const companyLogoContainer = ref<HTMLElement | null>(null)
const isCompanyLogoVisible = ref(false)
let companyLogoObserver: IntersectionObserver | null = null

const setupCompanyLogoObserver = () => {
  if (!props.testimonial.companyLogo) {
    isCompanyLogoVisible.value = false
    return
  }

  if (typeof window === 'undefined') {
    isCompanyLogoVisible.value = true
    return
  }

  if (!companyLogoContainer.value) {
    isCompanyLogoVisible.value = true
    return
  }

  if (!('IntersectionObserver' in window)) {
    isCompanyLogoVisible.value = true
    return
  }

  companyLogoObserver?.disconnect()
  isCompanyLogoVisible.value = false

  companyLogoObserver = new IntersectionObserver(
    (entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          isCompanyLogoVisible.value = true
          companyLogoObserver?.disconnect()
          companyLogoObserver = null
        }
      })
    },
    {
      rootMargin: '40px',
      threshold: 0.25,
    }
  )

  companyLogoObserver.observe(companyLogoContainer.value)
}

onMounted(async () => {
  if (!props.testimonial.companyLogo) {
    isCompanyLogoVisible.value = false
    return
  }

  await nextTick()
  setupCompanyLogoObserver()
})

onUnmounted(() => {
  companyLogoObserver?.disconnect()
  companyLogoObserver = null
})

// Utility function to format date
const formatDate = (dateString: string) => {
  const date = new Date(dateString)
  return date.toLocaleDateString('fr-FR', {
    year: 'numeric',
    month: 'long',
  })
}

// Computed property for company logo URL
const companyLogoUrl = computed(() => {
  if (!props.testimonial.companyLogo) return ''
  const img = props.testimonial.companyLogo.startsWith('/')
    ? props.testimonial.companyLogo.slice(1)
    : props.testimonial.companyLogo
  return import.meta.env.BASE_URL + img
})
</script>
