<template>
  <AppSection ref="sectionRef" id="contact" class="bg-white dark:bg-gray-900">
    <!-- En-tête de section -->
    <div class="mb-16 text-center">
      <h2 class="mb-4 text-3xl font-bold text-gray-900 dark:text-white md:text-4xl">
        {{ tUI('contact.title') }}
      </h2>
      <p class="mx-auto max-w-3xl text-lg text-gray-600 dark:text-gray-300">
        {{ tUI('contact.subtitle') }}
      </p>
    </div>

    <div class="grid grid-cols-1 gap-16 lg:grid-cols-2">
      <!-- Informations de contact -->
      <div>
        <h3 class="mb-8 text-2xl font-bold text-gray-900 dark:text-white">
          {{ tUI('contact.infoTitle') }}
        </h3>

        <div class="mb-8 space-y-6">
          <div
            v-for="contact in socialLinksStore.enabledContactInfo"
            :key="contact.key"
            class="flex items-center"
          >
            <div
              class="mr-4 flex h-12 w-12 items-center justify-center rounded-xl bg-blue-100 dark:bg-blue-900"
            >
              <Icon :name="contact.icon" class="text-blue-600 dark:text-blue-400" />
            </div>
            <div>
              <div class="text-sm text-gray-500 dark:text-gray-400">{{ contact.label }}</div>
              <a
                v-if="contact.type === 'email'"
                :href="`mailto:${contact.value}`"
                class="font-semibold text-gray-900 transition-colors hover:text-blue-600 dark:text-white dark:hover:text-blue-400"
              >
                {{ contact.value }}
              </a>
              <a
                v-else-if="contact.type === 'tel'"
                :href="`tel:${contact.value}`"
                class="font-semibold text-gray-900 transition-colors hover:text-blue-600 dark:text-white dark:hover:text-blue-400"
              >
                {{ contact.value }}
              </a>
              <div v-else class="font-semibold text-gray-900 dark:text-white">
                {{ contact.value }}
              </div>
            </div>
          </div>
        </div>

        <!-- Réseaux sociaux -->
        <div>
          <h4 class="mb-4 text-lg font-semibold text-gray-900 dark:text-white">
            {{ followMeText }}
          </h4>
          <SocialLinks
            :use-store="true"
            variant="bordered"
            size="lg"
            direction="horizontal"
            alignment="start"
          />
        </div>
      </div>

      <!-- Formulaire de contact -->
      <div>
        <ContactForm />
      </div>
    </div>
  </AppSection>
</template>

<script setup lang="ts">
import { computed } from 'vue'
import { useI18n as useVueI18n } from 'vue-i18n'
import { useI18n } from '@/composables/useI18n'
import { useSectionReveal } from '@/composables/useSectionReveal'
import { useSocialLinksStore } from '@/stores/social-links'
import AppSection from '@/components/layout/AppSection.vue'
import ContactForm from './ContactForm.vue'
import Icon from '@/components/common/Icon.vue'
import SocialLinks from '@/components/common/SocialLinks.vue'

const socialLinksStore = useSocialLinksStore()
const { tUI } = useI18n()
const { locale } = useVueI18n()

const followMeText = computed(
  () => socialLinksStore.getTexts(locale.value).contact.followMe ?? tUI('contact.followMe')
)

// Animation d'apparition de la section
const { sectionRef } = useSectionReveal({
  animation: 'fadeIn',
  duration: 800,
  delay: 150,
})
</script>
