<template>
  <section
    ref="heroSection"
    class="relative flex min-h-screen items-center justify-center overflow-hidden pt-8"
    :class="backgroundClass"
  >
    <!-- Animated Background -->
    <div ref="backgroundGradient" class="absolute inset-0 opacity-30"></div>

    <!-- Advanced Floating Particles Background -->
    <div ref="particleContainer" class="pointer-events-none absolute inset-0 overflow-hidden"></div>

    <div class="relative z-10 mx-auto max-w-7xl px-4 text-center sm:px-6 lg:px-8">
      <!-- Avatar with Advanced Morphing Animation -->
      <div class="mb-8" ref="avatarContainer">
        <div
          class="relative mx-auto mb-6 h-32 w-32"
          ref="avatarWrapper"
          :style="{ willChange: 'transform' }"
        >
          <!-- Avatar -->
          <div
            ref="avatarElement"
            class="relative h-full w-full scale-100 transform overflow-hidden rounded-full border-4 border-white opacity-100 shadow-lg dark:border-gray-700"
            :style="transformOpacityWillChange"
          >
            <Avatar
              :src="avatarImage"
              alt="Avatar"
              rounded="none"
              size="2xl"
              :eager="true"
              class="h-full w-full transition-transform duration-300"
            />
          </div>
        </div>
      </div>

      <!-- Name with Advanced Typewriter Effect -->
      <h1
        ref="nameElement"
        class="mb-6 text-5xl font-bold text-gray-900 opacity-0 dark:text-white md:text-6xl"
        :style="transformOpacityWillChange"
      >
        <span ref="fullName" class="typewriter-text"></span>
      </h1>

      <!-- Title -->
      <p
        ref="titleElement"
        v-if="personal"
        class="mx-auto mb-8 max-w-3xl translate-y-4 transform text-xl text-gray-600 opacity-0 dark:text-gray-300 md:text-2xl"
        :style="transformOpacityWillChange"
        v-html="personal.description"
      ></p>

      <p
        ref="subtitleElement"
        v-if="personal"
        class="mb-12 translate-y-4 transform text-lg text-gray-500 opacity-0 dark:text-gray-400"
        :style="transformOpacityWillChange"
      >
        {{ personal.subtitle }}
      </p>

      <!-- Animated Stats -->
      <div
        ref="statsElement"
        class="mb-12 flex translate-y-4 transform flex-wrap justify-center gap-8 opacity-0"
        :style="transformOpacityWillChange"
      >
        <div v-for="(stat, index) in stats" :key="stat.id" class="text-center">
          <div
            :ref="
              (el: any) => {
                if (el) statsRefs[index] = el
              }
            "
            :class="`text-3xl font-bold text-${stat.color}-600 dark:text-${stat.color}-400`"
            :data-target="stat.value"
          >
            0
          </div>
          <div class="text-sm text-gray-600 dark:text-gray-300">{{ stat.label }}</div>
        </div>
      </div>

      <!-- Enhanced CTA Buttons with Ripple Effect -->
      <div
        ref="ctaElement"
        class="flex translate-y-4 transform flex-col justify-center gap-4 opacity-0 sm:flex-row"
        :style="transformOpacityWillChange"
      >
        <UiButton
          v-if="cta?.primary"
          size="lg"
          @click="handleProjectsClick"
          @mousedown="handleRipple"
          class="hero-button relative overflow-hidden"
        >
          <FontAwesomeIcon :icon="cta.primary.icon" class="mr-2" />
          {{ cta.primary.text }}
        </UiButton>
        <UiButton
          v-if="cta?.secondary"
          variant="outline"
          size="lg"
          @click="handleContactClick"
          @mousedown="handleRipple"
          class="hero-button relative overflow-hidden"
        >
          <FontAwesomeIcon :icon="cta.secondary.icon" class="mr-2" />
          {{ cta.secondary.text }}
        </UiButton>
      </div>

      <!-- Technologies -->
      <div
        ref="techElement"
        class="mb-4 mt-16 translate-y-4 transform opacity-0"
        :style="transformOpacityWillChange"
      >
        <p class="mb-6 text-sm text-gray-500 dark:text-gray-400">
          {{ tUI('hero.favoriteTechnologies') }}
        </p>
        <div class="flex flex-wrap justify-center gap-4">
          <UiBadge
            v-for="(tech, index) in favoriteTechs"
            :key="tech.name"
            :ref="(el: any) => (techBadges[index] = el?.$el || el)"
            variant="secondary"
            :style="`color: ${tech.color}; animation-delay: ${index * 0.1}s`"
            class="tech-badge scale-50 transform cursor-pointer opacity-0"
          >
            <FontAwesomeIcon :icon="tech.icon" class="mr-2" />
            {{ tech.name }}
          </UiBadge>
        </div>
      </div>
    </div>
  </section>
</template>

<script setup lang="ts">
import { ref, onMounted, onUnmounted, computed, watch } from 'vue'
import { useAdvancedAnimations } from '@/composables/useAdvancedAnimations'
import { useLocalizedData } from '@/composables/useLocalizedData'
import { useI18n } from '@/composables/useI18n'
import { useI18n as useVueI18n } from 'vue-i18n'
import UiButton from '@/components/ui/UiButton.vue'
import UiBadge from '@/components/ui/UiBadge.vue'
import Avatar from '@/components/common/Avatar.vue'

// Advanced animations composable
const {
  morphingEntrance,
  createRippleEffect,
  smoothScrollTo,
  staggeredReveal,
  animateCounter,
  createParticleSystem,
} = useAdvancedAnimations()

const { loadHeroData } = useLocalizedData()
const { locale } = useVueI18n()

// Interfaces for data
interface Personal {
  name: string
  lastName: string
  description: string
  subtitle: string
}

interface Stat {
  id: string
  value: number
  label: string
  color: string
}

interface Tech {
  name: string
  color: string
  icon: string
}

interface CTA {
  primary: {
    icon: string
    text: string
    action: string
  }
  secondary: {
    icon: string
    text: string
    action: string
  }
}

// Template refs
const heroSection = ref<HTMLElement>()
const backgroundGradient = ref<HTMLElement>()
const particleContainer = ref<HTMLElement>()
const avatarContainer = ref<HTMLElement>()
const avatarWrapper = ref<HTMLElement>()
const lightCircle = ref<HTMLElement>()
const avatarElement = ref<HTMLElement>()
const nameElement = ref<HTMLElement>()
const fullName = ref<HTMLElement>()
const titleElement = ref<HTMLElement>()
const subtitleElement = ref<HTMLElement>()
const statsElement = ref<HTMLElement>()
const statsRefs = ref<HTMLElement[]>([])
const ctaElement = ref<HTMLElement>()
const techElement = ref<HTMLElement>()
const scrollIndicator = ref<HTMLElement>()
const techBadges = ref<HTMLElement[]>([])

// Animation state
const animationStarted = ref(false)
let particleCleanup: (() => void) | undefined
let parallaxCleanup: (() => void) | undefined
let gradientCleanup: (() => void) | undefined
let heroObserver: IntersectionObserver | null = null
const activeHolographicAnimations: Array<{ kill(): void }> = []

// Hero data reactive refs
const heroData = ref<Record<string, unknown> | null>(null)
const personal = ref<Personal | null>(null)
const stats = ref<Stat[] | null>(null)
const favoriteTechs = ref<Tech[]>([])
const cta = ref<CTA | null>(null)
const animationConfig = ref<Record<string, unknown> | null>(null)
const { tUI } = useI18n()

const transformOpacityWillChange = { willChange: 'transform, opacity' } as const

// L'avatar utilise import.meta.env.BASE_URL pour traiter le base correct
const avatarImage = import.meta.env.BASE_URL + 'images/avatars/avatar.webp'

// Function to load hero data
async function loadAndSetHeroData() {
  const data = await loadHeroData()
  if (data) {
    heroData.value = data as Record<string, unknown>
    personal.value = (data as Record<string, unknown>).personal as Personal
    stats.value = (data as Record<string, unknown>).stats as Stat[]
    favoriteTechs.value = (data as Record<string, unknown>).favoriteTechs as Tech[]
    cta.value = (data as Record<string, unknown>).cta as CTA
    animationConfig.value = (data as Record<string, unknown>).animations as Record<string, unknown>
  }
}

const triggerHeroAnimation = () => {
  if (!animationStarted.value) {
    void startAdvancedSequentialAnimation()
  }
}

// Reload hero data when language changes
watch(locale, async () => {
  await loadAndSetHeroData()
  animationStarted.value = false
  triggerHeroAnimation()
})

// Computed properties
const backgroundClass = computed(() => {
  return 'bg-gradient-to-br from-blue-50 to-indigo-100 dark:from-gray-900 dark:to-gray-800'
})

// Enhanced particle system
const initParticleSystem = () => {
  if (particleContainer.value) {
    particleCleanup = createParticleSystem(particleContainer.value, {
      count: 25,
      colors: ['#3B82F6', '#8B5CF6', '#EC4899', '#10B981', '#F59E0B'],
      size: { min: 1, max: 4 },
      speed: { min: 15, max: 35 },
      opacity: { min: 0.1, max: 0.4 },
    })
  }
}

// Enhanced gradient animation
const animateBackgroundGradient = () => {
  if (!backgroundGradient.value) return

  const colors = [
    'linear-gradient(45deg, rgba(59, 130, 246, 0.1) 0%, rgba(147, 51, 234, 0.1) 50%, rgba(236, 72, 153, 0.1) 100%)',
    'linear-gradient(90deg, rgba(16, 185, 129, 0.1) 0%, rgba(59, 130, 246, 0.1) 50%, rgba(147, 51, 234, 0.1) 100%)',
    'linear-gradient(135deg, rgba(236, 72, 153, 0.1) 0%, rgba(245, 158, 11, 0.1) 50%, rgba(16, 185, 129, 0.1) 100%)',
    'linear-gradient(180deg, rgba(147, 51, 234, 0.1) 0%, rgba(236, 72, 153, 0.1) 50%, rgba(59, 130, 246, 0.1) 100%)',
  ]

  let currentIndex = 0

  const animate = () => {
    if (backgroundGradient.value?.style) {
      const color = colors[currentIndex]
      if (color) {
        backgroundGradient.value.style.background = color
        currentIndex = (currentIndex + 1) % colors.length
      }
    }
  }

  const interval = setInterval(animate, 3000)
  animate() // Initial call

  return () => clearInterval(interval)
}

// Enhanced sequential animation
const startAdvancedSequentialAnimation = async () => {
  if (animationStarted.value) return
  animationStarted.value = true

  // Check for reduced motion preference
  const prefersReducedMotion = window.matchMedia('(prefers-reduced-motion: reduce)').matches

  if (prefersReducedMotion) {
    // Show all elements immediately without animation
    const elements = [
      lightCircle.value,
      avatarElement.value,
      nameElement.value,
      titleElement.value,
      subtitleElement.value,
      statsElement.value,
      ctaElement.value,
      techElement.value,
      scrollIndicator.value,
    ]

    elements.forEach((el) => {
      if (el) {
        el.style.opacity = '1'
        el.style.transform = 'none'
      }
    })

    // Set text content immediately
    if (fullName.value && personal.value) {
      fullName.value.innerHTML = `${personal.value.name} <span class="text-blue-600 dark:text-blue-400">${personal.value.lastName}</span>`
    }

    return
  }

  try {
    // 1. Enhanced morphing entrance for avatar
    if (avatarElement.value && lightCircle.value) {
      // Assurer que l'avatar est visible même si l'animation échoue
      avatarElement.value.style.opacity = '1'
      avatarElement.value.style.transform = 'scale(1)'

      // Light circle appears first
      lightCircle.value.style.opacity = '1'
      lightCircle.value.style.transform = 'scale(1.2)'
      await new Promise((resolve) => setTimeout(resolve, 300))

      // Avatar morphs in with advanced animation
      morphingEntrance(avatarElement.value, { delay: 200 })
      await new Promise((resolve) => setTimeout(resolve, 1000))
    }

    // 2. Custom typewriter effect for name (single cursor)
    if (fullName.value) {
      nameElement.value!.style.opacity = '1'

      // Custom typewriter implementation with single cursor using JSON data
      const firstNameText = (personal.value?.name || '') + ' '
      const lastNameText = personal.value?.lastName || ''
      fullName.value.innerHTML = ''

      // Add cursor
      const cursor = document.createElement('span')
      cursor.className = 'typewriter-cursor'
      cursor.textContent = '|'
      fullName.value.appendChild(cursor)

      // Type first name first
      for (let i = 0; i < firstNameText.length; i++) {
        await new Promise((resolve) =>
          setTimeout(
            resolve,
            ((animationConfig.value as Record<string, unknown>)?.typewriterSpeed as number) || 100
          )
        )
        fullName.value.insertBefore(document.createTextNode(firstNameText.charAt(i)), cursor)
      }

      // Create span for last name with blue color
      const lastNameSpan = document.createElement('span')
      lastNameSpan.className = 'text-blue-600 dark:text-blue-400'
      fullName.value.insertBefore(lastNameSpan, cursor)

      // Type last name inside the blue span
      for (let i = 0; i < lastNameText.length; i++) {
        await new Promise((resolve) =>
          setTimeout(
            resolve,
            ((animationConfig.value as Record<string, unknown>)?.typewriterSpeed as number) || 100
          )
        )
        lastNameSpan.appendChild(document.createTextNode(lastNameText.charAt(i)))
      }

      // Remove cursor after animation
      setTimeout(() => {
        if (cursor.parentNode) {
          cursor.parentNode.removeChild(cursor)
        }
      }, 1000)

      await new Promise((resolve) => setTimeout(resolve, 400))
    }

    // 3. Staggered reveal for text elements
    const textElements = [titleElement.value, subtitleElement.value].filter(
      Boolean
    ) as HTMLElement[]
    if (textElements.length > 0) {
      staggeredReveal(textElements, { stagger: 0.2, duration: 0.8 })
      await new Promise((resolve) => setTimeout(resolve, 600))
    }

    // 4. Enhanced counter animations
    if (statsElement.value) {
      statsElement.value.style.transition = 'all 0.6s ease'
      statsElement.value.style.opacity = '1'
      statsElement.value.style.transform = 'translateY(0)'

      // Wait a bit for the stats to be visible, then start counters
      await new Promise((resolve) => setTimeout(resolve, 300))

      // Start enhanced counters with reliable animation
      stats.value?.forEach((stat, index: number) => {
        const statElement = statsRefs.value[index]
        if (statElement) {
          // Reset to 0 first
          statElement.textContent = '0'
          // Start animation
          animateCounter(statElement, stat.value, {
            duration:
              ((animationConfig.value as Record<string, unknown>)?.counterDuration as number) ||
              2000,
            ease: 'power2.out',
          })
        }
      })

      await new Promise((resolve) => setTimeout(resolve, 800))
    }

    // 5. Enhanced CTA buttons
    if (ctaElement.value) {
      ctaElement.value.style.transition = 'all 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275)'
      ctaElement.value.style.opacity = '1'
      ctaElement.value.style.transform = 'translateY(0) scale(1)'
      await new Promise((resolve) => setTimeout(resolve, 400))
    }

    // 6. Tech badges with enhanced stagger
    if (techElement.value && techBadges.value.length > 0) {
      techElement.value.style.transition = 'all 0.6s ease'
      techElement.value.style.opacity = '1'
      techElement.value.style.transform = 'translateY(0)'

      // Enhanced staggered animation for tech badges
      staggeredReveal(techBadges.value, {
        stagger: 0.1,
        duration: 0.5,
        ease: 'elastic.out(1, 0.5)',
      })

      await new Promise((resolve) => setTimeout(resolve, 1000))
    }

    // 7. Scroll indicator
    if (scrollIndicator.value) {
      scrollIndicator.value.style.transition = 'all 0.6s ease'
      scrollIndicator.value.style.opacity = '1'
      scrollIndicator.value.style.transform = 'translateY(0)'
    }
  } catch {
    // Fallback : s'assurer que l'avatar est visible même en cas d'erreur
    if (avatarElement.value) {
      avatarElement.value.style.opacity = '1'
      avatarElement.value.style.transform = 'scale(1)'
    }
  }
}

// Enhanced button interactions
const handleRipple = (event: MouseEvent) => {
  const button = event.currentTarget as HTMLElement
  createRippleEffect(event, button, 'rgba(59, 130, 246, 0.3)')
}

const handleProjectsClick = () => {
  const action = cta.value?.primary.action
  if (action) smoothScrollTo(`#${action}`)
}

const handleContactClick = () => {
  const action = cta.value?.secondary.action
  if (action) smoothScrollTo(`#${action}`)
}

// Setup avatar mouse tracking (permanent eye tracking effect)
const setupAvatarMouseTracking = (avatar: HTMLElement) => {
  const handleMouseMove = (event: MouseEvent) => {
    const avatarRect = avatar.getBoundingClientRect()

    // Calculate avatar center
    const avatarCenterX = avatarRect.left + avatarRect.width / 2
    const avatarCenterY = avatarRect.top + avatarRect.height / 2

    // Calculate mouse position relative to avatar center
    const deltaX = event.clientX - avatarCenterX
    const deltaY = event.clientY - avatarCenterY

    // Calculate distance and apply limits
    const maxMovement = 8 // Maximum pixels the avatar can move
    const distance = Math.sqrt(deltaX * deltaX + deltaY * deltaY)
    const clampedDistance = Math.min(distance, 100) // Limit tracking distance

    // Calculate movement with intensity based on distance
    const intensity = clampedDistance / 100
    const moveX = (deltaX / distance) * maxMovement * intensity || 0
    const moveY = (deltaY / distance) * maxMovement * intensity || 0

    // Apply smooth transformation
    avatar.style.transition = 'transform 0.3s ease-out'
    avatar.style.transform = `translate(${moveX}px, ${moveY}px) scale(1)`
  }

  // Add event listener to the entire document for global tracking
  document.addEventListener('mousemove', handleMouseMove)

  // Return cleanup function
  return () => {
    document.removeEventListener('mousemove', handleMouseMove)
    avatar.style.transform = 'translate(0px, 0px) scale(1)'
  }
}

onMounted(async () => {
  await loadAndSetHeroData()

  // Fallback immédiat pour l'avatar
  window.setTimeout(() => {
    if (avatarElement.value) {
      avatarElement.value.style.opacity = '1'
      avatarElement.value.style.transform = 'scale(1)'
    }
  }, 100)

  // Setup avatar mouse tracking immédiatement (permanent eye tracking effect)
  window.setTimeout(() => {
    if (avatarWrapper.value) {
      parallaxCleanup = setupAvatarMouseTracking(avatarWrapper.value)
    }
  }, 200)

  // Initialize advanced features
  initParticleSystem()
  gradientCleanup = animateBackgroundGradient()

  if (typeof window !== 'undefined' && heroSection.value && 'IntersectionObserver' in window) {
    heroObserver?.disconnect()
    heroObserver = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            heroObserver?.disconnect()
            heroObserver = null
            triggerHeroAnimation()
          }
        })
      },
      {
        rootMargin: '0px 0px -20% 0px',
        threshold: 0.35,
      }
    )

    heroObserver.observe(heroSection.value)
  } else {
    triggerHeroAnimation()
  }
})

onUnmounted(() => {
  if (particleCleanup) particleCleanup()
  if (parallaxCleanup) parallaxCleanup()
  if (gradientCleanup) gradientCleanup()
  heroObserver?.disconnect()
  heroObserver = null

  // Clean up holographic animations
  activeHolographicAnimations.forEach((animation) => {
    if (animation && animation.kill) {
      animation.kill()
    }
  })
})
</script>

<style scoped>
/* Enhanced Typewriter cursor effect */
.typewriter-text {
  position: relative;
}

.typewriter-cursor {
  position: relative;
  animation: blink 1s infinite;
  opacity: 1;
  transition: opacity 0.5s ease;
  color: currentColor;
}

@keyframes blink {
  0%,
  50% {
    opacity: 1;
  }
  51%,
  100% {
    opacity: 0;
  }
}

/* Enhanced Ripple animation */
@keyframes ripple {
  to {
    transform: scale(4);
    opacity: 0;
  }
}

/* Enhanced button hover effects */
.hero-button {
  position: relative;
  overflow: hidden;
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transform-style: preserve-3d;
}

.hero-button::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition:
    width 0.6s ease,
    height 0.6s ease;
  z-index: 0;
}

.hero-button:hover::before {
  width: 300%;
  height: 300%;
}

.hero-button:hover {
  transform: translateY(-3px) scale(1.02);
  box-shadow:
    0 20px 40px rgba(0, 0, 0, 0.1),
    0 0 0 1px rgba(255, 255, 255, 0.05);
}

.hero-button:active {
  transform: translateY(-1px) scale(0.98);
}

/* Enhanced tech badge animations */
.tech-badge {
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transform-style: preserve-3d;
}

.tech-badge::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s ease;
}

.tech-badge:hover::before {
  left: 100%;
}

.tech-badge:hover {
  transform: scale(1.1) translateZ(10px);
  box-shadow:
    0 10px 25px rgba(0, 0, 0, 0.15),
    0 0 20px rgba(59, 130, 246, 0.3);
  border-color: rgba(59, 130, 246, 0.5);
}

/* Enhanced scroll indicator */
.animate-bounce {
  animation: enhancedBounce 2s infinite;
}

@keyframes enhancedBounce {
  0%,
  20%,
  53%,
  80%,
  100% {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    transform: translate3d(0, 0, 0);
  }
  40%,
  43% {
    animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    transform: translate3d(0, -15px, 0);
  }
  70% {
    animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    transform: translate3d(0, -7px, 0);
  }
  90% {
    transform: translate3d(0, -2px, 0);
  }
}

/* Responsive adjustments */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }

  .typewriter-cursor {
    animation: none;
    opacity: 1;
  }

  .animate-bounce {
    animation: none;
  }
}

/* Mobile optimizations */
@media (max-width: 768px) {
  .hero-button:hover {
    transform: none;
  }

  .tech-badge:hover {
    transform: scale(1.05);
  }
}

/* Enhanced accessibility */
@media (prefers-contrast: high) {
  .hero-button {
    border: 2px solid;
  }

  .tech-badge {
    border: 1px solid;
  }
}

/* Dark mode enhancements */
@media (prefers-color-scheme: dark) {
  .hero-button::before {
    background: rgba(255, 255, 255, 0.05);
  }

  .tech-badge:hover {
    box-shadow:
      0 10px 25px rgba(0, 0, 0, 0.3),
      0 0 20px rgba(59, 130, 246, 0.2);
  }
}
</style>
